<template>
    <div @click="remove_navs">
    <div class = "header">
        <ul class="stay headerlinks" v-if = "main_nav">
            <li><a class="stay" href = "#">Rent</a></li>
            <li><a class="stay" href = "#">Buy</a></li>
            <li><a class="stay" href = "#">Lease</a></li>
            <li><a class="stay" href = "#">Sell</a></li>
        </ul>
         
        <div class = "logo">
        <a href="#"><img src="@/assets/images/logo.png" id="logo" Alt = "Logo"></a>
        </div>

        <font-awesome-icon @click = "toggle_main_nav" class="remove"  icon="fa-solid fa-navicon" id="hamburger"/>
        
        <ul class="accesslinks">
        <li v-if = "signInbtn"><router-link to = "sign-in" id = "signInbtn">Sign in</router-link></li>
        <li v-if = "dashboardbtn" @click = "toggle_dashboard_nav" class="remove">Dashboard</li> 
        </ul>
</div>

<DASHBOARD :easycoins = "user.easycoins" v-if = "dashboard_nav"/> <br>

<MESSAGE v-if = "message">
    {{message}}
</MESSAGE>

<div class = "container">
    <h1 @click="listing_category_length">Listed Property</h1>
    <router-link to = "/dashboard/post-listing" id="add_property">Add property <font-awesome-icon icon="fa-solid fa-circle-plus" style="font-size:12px;"/></router-link>
    <div class="filter_action_flex">
        
    <div class="filter">
    <label for="filter">Show <font-awesome-icon icon="fa-solid fa-filter" style="font-size:12px;"/></label>
    <select id="filter"  v-model="filter" @change="filter_search">
        <option>All</option>
        <option>Unpublished listings</option>
        <option>Pending listings</option>
        <option>Published listings</option>
    </select>
    </div>

    <div class="delete">
    <label for="delete">Action <font-awesome-icon icon="fa-solid fa-trash" style="font-size:12px;"/></label>
    <button id="delete_btn" @click = "toggle_delete_nav" class="remove">Delete
       <span class = "select_delete" v-if = "delete_nav"> 
                <p @click="delete_listings_categorically"><font-awesome-icon icon="fa-solid fa-trash" style="font-size: 11px;"/>Delete All listings</p>
                <p @click="delete_listings_categorically"><font-awesome-icon icon="fa-solid fa-trash" style="font-size: 11px;"/>Delete Unpublished listings</p>
                <p @click="delete_listings_categorically"><font-awesome-icon icon="fa-solid fa-trash" style="font-size: 11px;"/>Delete Pending listings</p>
                <p @click="delete_listings_categorically"><font-awesome-icon icon="fa-solid fa-trash" style="font-size: 11px;"/>Delete Published listings </p>
    </span>
    </button>
    </div>

    </div>

    
    <div class = "listings">

    <template v-for = "listing in paginated_listings">
    <div class="houses">
    <img :src = "backendUrl + listing.pictures[0]">
    <div class="house_info">
    <h1><font-awesome-icon icon="fa-solid fa-home"/> {{listing.title}}</h1>
    <h2><font-awesome-icon icon="fa-solid fa-location"/> {{listing.location}} {{listing.town}}, {{listing.state}}.</h2>
    <p><font-awesome-icon icon="fa-solid fa-eye" style="font-size: 11px;"/> Views: <b>{{listing.views}}</b></p>
    <p><font-awesome-icon icon="fa-solid fa-calendar-days" style="font-size: 11px;"/> Date Added: <b>{{listing.dateAdded}}</b></p>
    <p><font-awesome-icon icon="fa-solid fa-fingerprint" style="font-size: 11px;"/> Property Id: <b>{{listing.id}}</b></p>
    <p><font-awesome-icon icon="fa-solid fa-clipboard-check" style="font-size: 11px;"/> Status: <b :style=" `${listing.status}` === 'Published' ? 'color: green;' : `${listing.status}` === 'Unpublished' ? 'color: red;' : 'color: gray;' ">{{listing.status}}</b></p>
    <p> Price: <b>{{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN' }).format(listing.price))}} {{listing.payment}}</b></p>
    </div>
    
    <hr> 

    <div class = "sub_info">
    <router-link class = "sub_info_child" :to = "{ name: 'view-listing', params: { id: listing.id } }" v-if = "listing.status === 'Published'"><font-awesome-icon icon="fa-solid fa-pen-to-square"/> View</router-link> 
    <router-link class = "sub_info_child" :to = "{ name: 'editListing', params: { id: listing.id } }" v-if = "listing.status === 'Unpublished'"><font-awesome-icon icon="fa-solid fa-pen-to-square"/> Edit</router-link> 
    <router-link class = "sub_info_child" :to = "{ name: 'postPictures', params: { id: listing.id } }" v-if = "listing.status === 'Unpublished'"><font-awesome-icon icon="fa-solid fa-images" /> Pictures</router-link>
    <router-link id = "publish_now" class= "sub_info_child" :to = "{ name: 'postPictures', params: { id: listing.id } }"  v-if = "listing.status === 'Unpublished'"><font-awesome-icon icon="fa-solid fa-upload" /> Publish
    <span class="publish_tooltip">Your property will be live and seen by potential customers immediately.</span>
    </router-link> 
    <p @click = "remove_listing(listing.id)" class = "sub_info_child" to = "#"><font-awesome-icon icon="fa-solid fa-car fa-trash"/> Delete</p>
    </div>
    </div>
    </template>

    </div>

    <h1 v-if = "paginated_listings.length === 0" id="no_property">No listed property</h1>


   <div class = "pagination" v-if = "total_pages !== 1">
       <p ref="previous_page" style="color: black;" @click="previous_page"><font-awesome-icon icon="fa-solid fa-arrow-left-long"/></p>
       <p style = "font-size: 17px;">Page {{page_number}} of {{total_pages}}</p>
       <p ref="next_page" style="color: black;" @click="next_page"><font-awesome-icon icon="fa-solid fa-arrow-right-long"/></p>
   </div>

    <!-- STATUS MEANING -->
     
    <div class = "status_meaning">
      <ul>
          <li style="color: green;">Published - <b style="color: black; font-weight: lighter;"> property has been approved and will be seen by site visitors.</b></li>
          <li style="color: gray;">Pending - <b style="color: black; font-weight: lighter;"> property is been reviewed, and awaiting approval.</b></li>
          <li style="color: red;">Unpublished - <b style="color: black; font-weight: lighter;"> property was rejected or has not been submitted for review.</b></li>
      </ul>
    </div>

    <!-- STATUS MEANING -->

<!-- STATISTIC CHART -->
    <section class="chart-list" style = "color: rgb(124, 134, 142); font-size: 15px;">
      <section class="chart-container">
        <vue-anychart :options=" {
          'chart': {
            'type': 'column',
            'series': [{
            'seriesType': 'column',
            'name': 'Total',
            'data':  [
            {'x': 'Published Listings', 'value':  `${listing_category_length('Published')}`},
            {'x': 'Unpublished Listings', 'value': `${listing_category_length('Unpublished')}`},
            {'x': 'Pending Listings', 'value': `${listing_category_length('Pending')}`}
          ], 
            }],
            'title': 'Listing Statistics',
        }
    }" ref="columnChart"></vue-anychart>
      </section>
      <p>Published Listings: {{listing_category_length('Published')}}</p>
      <p>Unpublished Listings: {{listing_category_length('Unpublished')}}</p>
      <p>Pending Listings: {{listing_category_length('Pending')}}</p>
      <p style="font-weight: bolder;">Total Listings: {{listing_category_length('Pending') + listing_category_length('Published') + listing_category_length('Unpublished')}}</p>
      </section>
  <!-- STATISTIC CHART -->

</div>
 <FOOTER/>
      </div>
</template>

<script>
import FOOTER from "@/components/Footer.vue"
import MIDDLEWARE from "@/middlewares/middlewares.js"
import API from "../api";
import DASHBOARD from "@/components/Dashboard.vue"

//ANY CHART LIBARY
import VueAnychart from '@/components/VueAnychart'
import Anychart from 'anychart'
import MESSAGE from "@/components/Message.vue"

 export default {
     name: "manageListings",
     components: {
         FOOTER,
         DASHBOARD,
         VueAnychart,
         MESSAGE,
     },
     data () {
         return {
          main_nav: false,
          dashboard_nav: false,
          delete_nav: false,
          signInbtn: undefined,
          dashboardbtn: undefined,
          message: undefined,
          paginated_listings: undefined,
          all_listings: undefined,
          user: undefined,
          filter: undefined,
          page_number: undefined,
          total_pages: undefined,
          backendUrl: 'http://localhost:9000/',

          /* ANY CHART */
          Anychart: Anychart,
          columnOptions: undefined,
        /* ANY CHART */
       }
         },
      methods: {

    screenhandler (){
          this.main_nav = MIDDLEWARE.screenhandler(window.innerWidth, 700)
     },

    toggle_main_nav () {
       this.main_nav = !this.main_nav
       this.dashboard_nav = false
       this.delete_nav = false
    },

    toggle_dashboard_nav () {
       this.dashboard_nav = !this.dashboard_nav
       this.main_nav = false
       this.delete_nav = false
    },

    toggle_delete_nav () {
       this.delete_nav = !this.delete_nav
       this.dashboard_nav = false
       this.main_nav = false
    },

     remove_navs (e) { //USED TO REMOVE ALL NAV BARS WHEN USER CLICKS ANY WHERE
      
      const class_name = e.target.className.split(" ")[0];
      
      if (class_name !== "remove") { //if the element clicked does not have the classname (remove), hide every nav on the page.
          
        this.dashboard_nav = false
        this.delete_nav = false

      }

     this.main_nav = MIDDLEWARE.removenav(e, window.innerWidth, 700)

    }, 

    show() {
         this.message = this.$route.params.message; 
         window.setTimeout( () => {
             this.message = undefined
          }, 3000)
    },

     remove_anychart_txt() { //used to remove that anychart default text
       window.setInterval( () => {
            this.$refs.columnChart.$el.children[0].children[1].children[0].remove()
       },1000)
    },  
    
    LoggedIn(response_message) {
          if (response_message === true) {
              this.signInbtn = false
              this.dashboardbtn = true
          } else {
              this.signInbtn =  true
              this.dashboardbtn = false
              this.$router.push({name: "home"}) // protects page against unlogged users
          }
      },


   //count pending, published and unpublished listings the attach result to chart
     listing_category_length(status) {
      
     const total = this.all_listings.filter( function (listing) {
        
        return listing.status === status 
      
      })
    
        return total.length;
    },


   async remove_listing(listing_id) {

       if (window.confirm("Do you want to delete property?")) {

       const response = await API.delete_listing({data: {listing_id: listing_id} });

       if (response.message === "delete succesful") {

        window.location.replace(`http://localhost:8080/dashboard/manage-listings?page=1`)
           
       } else {

        alert("Something went wrong, try again")
           
       }

       } else {

           return

       }

    },

    
  async delete_listings_categorically(e) { 

    switch (e.target.innerText) {

      case "Delete All listings":

      if (window.confirm("All your properties will be deleted, do you wish to continue?")) {
       
       const response = await API.delete_listings_categorically({data: {category: e.target.innerText} });

       window.location.assign(`http://localhost:8080/dashboard/manage-listings?page=1`)
       
      } 

      break;

      case "Delete Unpublished listings":

      if (window.confirm("Only unpublished properties will be deleted, do you wish to continue?")) {
       
       const response = await API.delete_listings_categorically({data: {category: e.target.innerText} });
        
       window.location.assign(`http://localhost:8080/dashboard/manage-listings?page=1`)

     } 

      break;

      case "Delete Pending listings":

      if (window.confirm("Only pending properties will be deleted, do you Wish to continue?")) {
       
       const response = await API.delete_listings_categorically({data: {category: e.target.innerText} });
        
        window.location.assign(`http://localhost:8080/dashboard/manage-listings?page=1`)

      } 

      break;

      case "Delete Published listings":

      if (window.confirm("Only published properties will be deleted, do you Wish to continue?")) {
       
       const response = await API.delete_listings_categorically({data: {category: e.target.innerText} });
        
       window.location.assign(`http://localhost:8080/dashboard/manage-listings?page=1`)

      } 

      break;

    }

    },

    next_page () {

     if (this.$route.query.page != this.total_pages) {

      window.location.assign(`http://localhost:8080/dashboard/manage-listings?page=${++this.$route.query.page}`)

      this.paginated_listings = respnse.message
         
     } else {

      this.$refs.next_page.attributes[1].nodeValue = "color: gray;"

      }

},

    previous_page () {

      if (this.$route.query.page != 1) {
            
      window.location.assign(`http://localhost:8080/dashboard/manage-listings?page=${--this.$route.query.page}`)

      this.paginated_listings = respnse.message

      } else {

      this.$refs.previous_page.attributes[1].nodeValue = "color: gray;"

      }

   },

   async filter_search () {

    const respond = await API.get_managelisting_page(this.$route.query.page, this.filter); //fetch listings according to the current category and page number

    this.paginated_listings = respond.paginatedListings //fetch all listings     
    
    this.total_pages = respond.total_pages

    this.page_number = this.$route.query.page;

     window.location.assign(`http://localhost:8080/dashboard/manage-listings?page=1`)

    const response = await API.filter({parameter: this.filter})

    } 
 
 },

   async mounted () {

          const user = await API.fetch_user() //fetch user account

          this.user = user.message //fetch the user
          
          this.filter = user.message.settings.filterListings; //set category to current value in db

          const respond = await API.get_managelisting_page(this.$route.query.page, this.filter); //fetch listings according to the current category and page number

          this.paginated_listings = respond.paginatedListings //fetch all listings          

          this.all_listings = respond.all_listings

          this.total_pages = respond.total_pages

          this.page_number = this.$route.query.page 
       
          this.screenhandler()
          
          window.addEventListener("resize", this.screenhandler)

          const response = await API.loggedIn();
          
          this.LoggedIn(response.message);

          this.show()

          this.remove_anychart_txt()
          
},
     unmounted () {

        window.removeEventListener("resize", this.screenhandler)
   
   },

 }
</script>

