import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Contact from '../views/Contact.vue'
import Avoidscams from '../views/Avoidscams.vue'
import Policies from '../views/Policies.vue'
import SignIn from '../views/Sign-in.vue'
import EmailActivation from '../views/Email-Activation.vue'
import EmailConfirmation from '../views/Email-Confirmation.vue'
import Error from '../views/Error.vue'
import Properties from '../views/Properties.vue'
import ReferralSignup from '../views/Referral-Signup.vue'


//DASHBOARD PAGES
import Profile from '../views/Profile.vue'
import PostListing from '../views/Post-Listing.vue'
import EditListing from '../views/Edit-Listing.vue'
import PostPictures from '../views/Post-Pictures.vue'
import ManageListings from '../views/Manage-Listings.vue'
import ViewListing from '../views/View-Listing.vue'
import SavedProperties from '../views/Saved-Properties.vue'
import Review from '../views/Review.vue'
import InviteFriends from '../views/Invite-Friends.vue'
import Referrals from '../views/Referrals.vue'
import BuyEasycoin from '../views/Buy-Easycoin.vue'
import PaymentConfirmation from '../views/Payment-Confirmation.vue'


//ADMIN PAGES
import admin from '../views/boss/admin.vue'
import adminView from '../views/boss/adminView.vue'

Vue.use(VueRouter)

const routes = [
//ADMIN PAGES
{
  path: '/private/admin',
  name: 'admin',
  component: admin
},

{
  path: '/private/admin/listing/:id',
  name: 'adminView',
  component: adminView
},


//PUBLIC PAGES
  {
    path: '/properties/:category',
    name: 'properties',
    component: Properties
  },

  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/about',
    name: 'about',
    component: About
  },
  {
    path: '/contact',
    name: 'contact',
    component: Contact
  },
  {
    path: '/avoidscams',
    name: 'avoidscams',
    component: Avoidscams
  },
  {
    path: '/policies',
    name: 'policies',
    component: Policies
  },
  {
    path: '/sign-in',
    name: 'sign-in',
    component: SignIn
  },
  {
    path: '/email-activation',
    name: 'email-activation',
    component: EmailActivation
  },
  {
    path: '/email-confirmation/:id',
    name: 'email-confirmation',
    component: EmailConfirmation
  },
  {
    path: '/error',
    name: 'error',
    component: Error
  },

  {
    path: '/view-listing/:id',
    name: 'view-listing',
    component: ViewListing
  },

  {
    path: '/referral-signup/:id',
    name: 'referral-signup',
    component: ReferralSignup
  },

  //DASHBOARD PAGES
  {
    path: '/dashboard/profile',
    name: 'profile',
    component: Profile
  },
  {
    path: '/dashboard/post-pictures/:id',
    name: 'postPictures',
    component:  PostPictures
  },
  {
    path: '/dashboard/post-listing',
    name: 'postListing',
    component:  PostListing
  },
  {
    path: '/dashboard/manage-listings',
    name: 'manageListings',
    component:  ManageListings
  },
  {
    path: '/dashboard/saved-properties',
    name: 'savedProperties',
    component:  SavedProperties
  },
  {
    path: '/dashboard/review',
    name: 'review',
    component:  Review
  },
  {
    path: '/dashboard/edit-listing/:id',
    name: 'editListing',
    component: EditListing
  },
  {
    path: '/dashboard/invite-friends',
    name: 'inviteFriends',
    component: InviteFriends
  },
  {
    path: '/dashboard/referrals',
    name: 'referrals',
    component: Referrals
  },

  {
    path: '/dashboard/buy-easycoin',
    name: 'buyEasycoin',
    component: BuyEasycoin
  },

  {
    path: '/dashboard/payment-confirmation',
    name: 'paymentConfirmation',
    component: PaymentConfirmation
  },


]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: 'processbar_active_link'
})

export default router
