<template>
   <div @click = "removenav">
    <div class = "header">
        <ul class="stay headerlinks" v-if = "toggle">
            <li><a class="stay" href = "#">Rent</a></li>
            <li><a class="stay" href = "#">Buy</a></li>
            <li><a class="stay" href = "#">Lease</a></li>
            <li><a class="stay" href = "#">Sell</a></li>
        </ul>
         
        <div class = "logo">
        <a href="#"><img src="@/assets/images/logo.png" id="logo" Alt = "Logo"></a>
        </div>

        <font-awesome-icon @click = "toggle = !toggle"  icon="fa-solid fa-navicon" id="hamburger"/>
        
        <ul class="accesslinks">
        <li class = "loginbtn"><router-link to = "sign-in">Sign in</router-link></li>
        </ul>
</div>
         
       <div class="switchlink">
            <p
            v-for="tab in tabs"
            :key="tab"
            :class="['tab-button', { active: currentTab === tab }]"
            class = "link"
            @click="currentTab = tab"
            >
             {{ tab }}
    </p>
        </div>
         <component :is="currentTab"/>

       <FOOTER /> 
    </div>
</template>

<script>
import FOOTER from "@/components/Footer.vue"
import MIDDLEWARE from "@/middlewares/middlewares.js"
import Login from "@/components/Login.vue"
import Register from "@/components/Register.vue"
import API from "../api";

export default {
  name: 'sign-in',
  components: {
      FOOTER,
      Login,
      Register,
  },
  data () {
       return {
          currentTab: 'Login',
          tabs: ['Login', 'Register'],
          toggle: false,
          spinner: false,
          disablebtn: false,
          screenwidth: undefined,
          backend_err: false,
          user: undefined,

          formvalues: {
              email: "",
              password: "",
          },
          formvalues_err: {
              email_err: "",
              password_err: "",
          } 
       }
   },

   methods: {

      screenhandler (){
          this.toggle = MIDDLEWARE.screenhandler(this.screenwidth, window.innerWidth, 612)
       },

      removenav (e) {
          this.toggle = MIDDLEWARE.removenav(e, this.screenwidth, window.innerWidth, 612)
      },

      dissapear() {
          window.setTimeout( () => {
             this.backend_err = false;
          }, 5000)
      },

       LoggedIn(response_message) {
          if (response_message === true) {
              this.$router.push({name: "home"}) // protects page against unlogged users
          } else {
              this.$router.push({name: "sign-in"}) // protects page against unlogged users
          }
      },

    emailvalidated() {
      if (this.formvalues.email === "") {
        this.formvalues_err.email_err = "Please fill field";
      } else {
        this.formvalues_err.email_err = "";
        return true;
      }
    },  

    passwordvalidated() {
      if (this.formvalues.password === "") {
        this.formvalues_err.password_err = "Please fill field";
      } else {
        this.formvalues_err.password_err = "";
        return true;
      }

    },

    async validation () {
        if (this.emailvalidated() && this.passwordvalidated()) {
           this.spinner = true;
           this.disablebtn = true;
           const response = await API.login(this.formvalues);
     
       if (response.message === "Invalid Email") {
            this.backend_err = response.message
            this.disablebtn = false;
            this.spinner = false;
            this.dissapear()
         }

        else if (response.message === "Details Correct, But Account Unverified") {      
            this.backend_err = "Verify Your Account, and Try Again"
            this.disablebtn = false;
            this.spinner = false;
            this.dissapear()
        }

        else if (response.message === "Logged in Succesfully") {      
             this.$router.push({ name: 'home', params: {message: `Welcome ${response.user.firstname}`} })
        }

        else if (response.message === "Incorrect Password") {      
            this.backend_err = response.message
            this.disablebtn = false;
            this.spinner = false;
            this.dissapear()
        }

        else {
            this.backend_err = response.message    //If there is an error from server
            this.disablebtn = false;
            this.spinner = false;
            this.dissapear()
        }
 
      }

    },

},
      //LIFE CYCLE HOOKS
    updated() {
        this.emailvalidated();
        this.passwordvalidated();
},
   async  mounted () {

        this.screenhandler()

        window.addEventListener("resize", this.screenhandler)

        const response = await API.loggedIn();

        this.user = response.user
          
        this.LoggedIn(response.message)
},
     unmounted () {
        window.removeEventListener("resize", this.screenhandler)
},
}
</script>


