<template>

    <div @click="remove_navs">
    <div class = "header">
        <ul class="stay headerlinks" v-if = "main_nav">
            <li><a class="stay" href = "#">Rent</a></li>
            <li><a class="stay" href = "#">Buy</a></li>
            <li><a class="stay" href = "#">Lease</a></li>
            <li><a class="stay" href = "#">Sell</a></li>
        </ul>
         
        <div class = "logo">
        <a href="#"><img src="@/assets/images/logo.png" id="logo" Alt = "Logo"></a>
        </div>

        <font-awesome-icon @click = "toggle_main_nav" class="remove"  icon="fa-solid fa-navicon" id="hamburger"/>
        
        <ul class="accesslinks">
        <li v-if = "signInbtn"><router-link to = "sign-in" id = "signInbtn">Sign in</router-link></li>
        <li v-if = "dashboardbtn" @click = "toggle_dashboard_nav" class="remove">Dashboard</li> 
        </ul>
</div>

<DASHBOARD :easycoins = "user.easycoins" v-if = "dashboard_nav"/>

 <div class="container">
 <div class = "flex_container">
    <div class="pictures">
        <h1>Add Pictures ({{pictures.length}})</h1>
     
    <template v-for = "filename in pictures">
    <div class="photos" ref="photos" style = "display: inline-block;">
        <img :src = "backendUrl + filename">
    <p @click = "deletePictures">Delete <font-awesome-icon icon="fa-solid fa-trash" id = "cancel"/></p>
    </div>
    </template>

    <vue-dropzone ref = "myVueDropzone"
      :options="dropzoneOptions"
      :include-styling="true"
      @vdropzone-thumbnail = "thumbnail"
      @vdropzone-success-multiple = "uploadSuccess"
      @vdropzone-error-multiple = "uploadFailure"
      @vdropzone-sending = "sendParam" 
      @vdropzone-files-added = "fileAdded"
      @vdropzone-mounted = "dropzoneMounted"
      @vdropzone-total-upload-progress = "totalUploadProgress"
      id="customdropzone"     
      :useCustomSlot= true>

     <div class="dropzone-custom-content">
        <h3 class="dropzone-custom-title"  style="margin: 0px 0 10px 0;">Drag and drop to upload pictures</h3>
        <div class="subtitle">or click to select a file from your computer</div>
        <div class="error" style="margin-top: 23px;">
        <p style="font-size: 11px; color: red; margin: 0 0 4px 0;">{{filetype_err}}</p>
        <p style="font-size: 11px; color: red; margin: 0 0 4px 0;">{{filesize_err}}</p>
        <p style="font-size: 11px; color: red; margin: 0 0 4px 0;">{{maxfile_err}}</p>
      </div>
     </div>
    </vue-dropzone>

    <p style="margin: 10px 0 0 0; color: gray; font-size: 12px;">Atleast 3 pictures for a property is required</p>
    <p style="margin: 10px 0 0 0; color: gray; font-size: 12px;">Hold CTRL to add multiple pictures at once on Laptops or Desktops</p>
    <p style="margin: 10px 0 0 0; color: gray; font-size: 12px;">Maximum of 30 pictures</p>
    <p style="margin: 10px 0 0 0; color: gray; font-size: 12px;">Max 10MB per photo in .PNG, .JPG or .JPEG </p>
     
    <div class="dropzone-previews dropzone pics" ref = "pix_preview" style="display: inline-block;"> <!--display image dropzone previews-->
    </div>
    
    </div>

    <div class="property_info">
     <h1>Property Info</h1>
     <hr>

    <h2>{{listing.title}}</h2>

     <b>Property id</b>
     <p>{{listing.id}}</p>

     <b>Category</b>
     <p>{{listing.category}}</p>

     <b>Type</b>
     <p>{{listing.type}}</p>

     <b>Location</b>
     <p>{{listing.location}}, {{listing.town}}, {{listing.state}}.</p>

     <b>Price</b>
     <p>{{listing.price}} {{listing.payment}}</p>

    <router-link class = "sub_info_child" :to = "{ name: 'editListing', params: { id: listing.id } }">Edit Info</router-link>

    

    </div>
</div>
<div class="buttons">

<button @click="publish" id="publish_now">Publish now
  <span class="publish_tooltip">Your property will be live and seen by potential customers immediately.</span>
</button>

<router-link :to = "{ name: 'manageListings', query: {page: 1}, params: { message: 'Listing saved succesfully' } }" id = "later">Later
  <span class="later_tooltip">Your property will be saved, but not be seen by potential customers until when published.</span>
</router-link>
</div>
</div>
 <FOOTER/>
</div>
</template>

<script>
import FOOTER from "@/components/Footer.vue"
import MIDDLEWARE from "@/middlewares/middlewares.js"
import API from "../api";
import DASHBOARD from "@/components/Dashboard.vue"

import vue2Dropzone from 'vue2-dropzone'
import 'vue2-dropzone/dist/vue2Dropzone.min.css'


 export default {
     name: "postPictures",
     components: {
         FOOTER,
         DASHBOARD,
         vueDropzone: vue2Dropzone,
     },
     data () {
         return {
          main_nav: false,
          dashboard_nav: false,
          screenwidth: undefined,
          signInbtn: undefined,
          dashboardbtn: undefined,
          listing: undefined,
          pictures: undefined,
          backendUrl: 'http://localhost:9000/',
          user: undefined,

        //DROP ZONE ERRORS
         filetype_err: undefined,
         filesize_err: undefined,
         maxfile_err: undefined,

        //DROP ZONE OBJECT
          dropzoneOptions: {
          url: '/submit_pictures',
          paramsName: "file",
          maxFilesize: 10, //10mb
          autoProcessQueue: false,
          acceptedFiles: "image/jpeg,image/jpg,image/png",
          previewTemplate: this.template(),
          uploadMultiple: true,
          parallelUploads: 1,
          previewsContainer: ".pics",
       }
       //DROP ZONE OBJECT
    }
},
    methods: {
    
      template: function () {
        return `<div class="dz-preview dz-file-preview" style = "display: inline-block; width: 100px; height: 100px; margin: 10px;">
                <div class="dz-image" style = "width: 100%; height:auto; color:  black; padding:0; margin: 10px; font-size: 12px; text-align: center; border-radius: 5px;">
                    <img data-dz-thumbnail-bg style = "display: block; z-index: 0; object-fit: cover; width: 100%; height: 80px;></div>
                </div>
                <div class="dz-details">
                    <div class="dz-size" margin-bottom: 1em; font-size: 13px;><span data-dz-size></span></div>
                    <div class="dz-filename"><span data-dz-name></span></div>
                </div>
                <div class="dz-progress" style = "display: inline-block;"><span style = "display: inline-block; color: black; yellow: black; width: 100px; margin: auto;" class="dz-upload" data-dz-uploadprogress></span></div>
                <div class="dz-error-message"><span data-dz-errormessage></span></div>
                <div class="dz-success-mark"><i class="fa fa-check"></i></div>
                <div class="dz-error-mark"><i class="fa fa-close"></i></div>
            </div>
        `;
      },

      thumbnail: function(file, dataUrl) {
        var j, len, ref, thumbnailElement;
        if (file.previewElement) {
            file.previewElement.classList.remove("dz-file-preview");
            ref = file.previewElement.querySelectorAll("[data-dz-thumbnail-bg]");
            for (j = 0, len = ref.length; j < len; j++) {
                thumbnailElement = ref[j];
                thumbnailElement.alt = file.name;
                thumbnailElement.src = dataUrl;
            }
            return setTimeout(((function(_this) {
                return function() {
                    return file.previewElement.classList.add("dz-image-preview");
                };
            })(this)), 1);
        }
      }, 

    uploadSuccess:  function (files, response) {
       this.pictures = response.message;
       this.$refs.photos.forEach( (photo_div) => {
          photo_div.attributes[2].nodeValue = "display: inline-block;" //make new files display after setting photos div to display none in delete pictures method
          photo_div.lastChild.innerHTML = 'Delete <svg data-v-0a97cd18="" aria-hidden="true" focusable="false" data-prefix="fas" data-icon="trash" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512" id="cancel" class="svg-inline--fa fa-trash"><path data-v-0a97cd18="" fill="currentColor" d="M135.2 17.7L128 32H32C14.3 32 0 46.3 0 64S14.3 96 32 96H416c17.7 0 32-14.3 32-32s-14.3-32-32-32H320l-7.2-14.3C307.4 6.8 296.3 0 284.2 0H163.8c-12.1 0-23.2 6.8-28.6 17.7zM416 128H32L53.2 467c1.6 25.3 22.6 45 47.9 45H346.9c25.3 0 46.3-19.7 47.9-45L416 128z" class=""></path></svg>' //add icon to the p element
       }) 
       files.forEach( (file, index) => {
          file.previewElement.attributes[1].nodeValue = "display: none; width: 100px; height: 100px; margin: 10px;" //make previews dissapear after upload
       })
     },

    uploadFailure: function (files, message, xhr) {          

        let maxfilesize_str = message.slice(-20)

        if (maxfilesize_str === "Max filesize: 10MiB.") {
        this.filesize_err = "Pictures larger than 10mb were rejected."
        files.forEach((file, index) => {
        file.previewElement.attributes[1].nodeValue = "display: none; width: 100px; height: 100px; margin: 10px;"
        });
        this.Errortimeout();
        } 
        
        if (message === "You can't upload files of this type.") {
           message = "Only jpg, png, jpeg files are allowed, others were rejected."
           this.filetype_err = message;
           files.forEach((file, index) => {
           file.previewElement.attributes[1].nodeValue = "display: none; width: 100px; height: 100px; margin: 10px;"
           });
           this.Errortimeout();
        } 
        
   },

   sendParam: function (file, xhr, formData) { //Sends listing id to backend
       formData.append("listing_id", this.listing.id)
   },

    Errortimeout () {
       window.setTimeout( () => { 
          this.filetype_err = ""
          this.filesize_err = ""
          //this.maxfile_err  = ""
       }, 16000)
   },
    
   maximumListing: async function () {

       const respond = await API.submit_pictures({listing_id: this.listing.id}) //fetch all the listing's pictures
       
       this.pictures = respond.message

       let pictures_In_db = this.pictures.length

       let pictures_spot_left = 30 - pictures_In_db;
       
       let pictures_In_queue = this.$refs.myVueDropzone.getQueuedFiles().length;

       if (pictures_In_queue  > pictures_spot_left) {
             
        let counter = pictures_In_queue - 1; //starts from the last file in the queue
       
        while ((counter) > (pictures_spot_left - 1)) {

           this.$refs.myVueDropzone.getQueuedFiles()[counter].previewElement.attributes[1].nodeValue = "display: none; width: 100px; height: 100px; margin: 10px;"

           this.$refs.myVueDropzone.getQueuedFiles()[counter].status = "error"

           counter--;
       }

       this.$refs.myVueDropzone.processQueue()

       this.maxfile_err = "Listing cannot have more than 30 pictures"

       } else {

       this.$refs.myVueDropzone.processQueue()

       this.maxfile_err = ""
      
       } 
       
      },

   fileAdded: function (file) {
       this.$refs.pix_preview.attributes[2].nodeValue = "display: inline-block;" //hide picture preview div
       this.maximumListing();
   },

   dropzoneMounted: function () {
       this.$refs.pix_preview.attributes[2].nodeValue = "display: none;" //hide picture preview div
   },

   totalUploadProgress: function (totaluploadprogress, totalBytes, totalBytesSent) {
    if (totaluploadprogress === 100) {
       this.$refs.pix_preview.attributes[2].nodeValue = "display: none;" //hide picture preview div
     }
   },
    
    deletePictures: async function (e) {

        const tag = e.target.nodeName;

        let elem, element, imgsrc, response;

         switch (tag) {

            case "P":
                element = e;
                elem = element.srcElement.parentElement.children[0].currentSrc.split("/");
                imgsrc = elem[elem.length - 1]
                element.srcElement.innerHTML = 'Removing...'  //change button to removing
                response = await API.delete_pictures({data: {imgsrc: imgsrc, listing_id: this.listing.id} });
                element.srcElement.innerHTML = 'Delete <svg data-v-0a97cd18="" aria-hidden="true" focusable="false" data-prefix="fas" data-icon="trash" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512" id="cancel" class="svg-inline--fa fa-trash"><path data-v-0a97cd18="" fill="currentColor" d="M135.2 17.7L128 32H32C14.3 32 0 46.3 0 64S14.3 96 32 96H416c17.7 0 32-14.3 32-32s-14.3-32-32-32H320l-7.2-14.3C307.4 6.8 296.3 0 284.2 0H163.8c-12.1 0-23.2 6.8-28.6 17.7zM416 128H32L53.2 467c1.6 25.3 22.6 45 47.9 45H346.9c25.3 0 46.3-19.7 47.9-45L416 128z" class=""></path></svg>' //add icon to the p element
                this.maximumListing()
                break;
         
            case "path":
                element = e;
                elem = element.srcElement.parentElement.parentElement.parentElement.children 
                imgsrc = elem[0].currentSrc.split("/")[elem[0].currentSrc.split("/").length - 1];
                elem[1].innerHTML = 'Removing...'//change button to removing
                response = await API.delete_pictures({data: {imgsrc: imgsrc, listing_id: this.listing.id } });
                elem[1].innerHTML = 'Delete <svg data-v-0a97cd18="" aria-hidden="true" focusable="false" data-prefix="fas" data-icon="trash" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512" id="cancel" class="svg-inline--fa fa-trash"><path data-v-0a97cd18="" fill="currentColor" d="M135.2 17.7L128 32H32C14.3 32 0 46.3 0 64S14.3 96 32 96H416c17.7 0 32-14.3 32-32s-14.3-32-32-32H320l-7.2-14.3C307.4 6.8 296.3 0 284.2 0H163.8c-12.1 0-23.2 6.8-28.6 17.7zM416 128H32L53.2 467c1.6 25.3 22.6 45 47.9 45H346.9c25.3 0 46.3-19.7 47.9-45L416 128z" class=""></path></svg>' //add icon to the p element
                this.maximumListing()
                break;

             default:

                break;
                 
         }

    },

//DROP ZONE

    screenhandler (){
          this.main_nav = MIDDLEWARE.screenhandler(window.innerWidth, 700)
     },

    toggle_main_nav () {
       this.main_nav = !this.main_nav
       this.dashboard_nav = false
    },

    toggle_dashboard_nav () {
       this.dashboard_nav = !this.dashboard_nav
       this.main_nav = false
    },

     remove_navs (e) { //USED TO REMOVE ALL NAV BARS WHEN USER CLICKS ANY WHERE
      
      const class_name = e.target.className.split(" ")[0];
      
      if (class_name !== "remove") { //if the element clicked does not have the classname (remove), hide every nav on the page.
          
        this.dashboard_nav = false

      }

     this.main_nav = MIDDLEWARE.removenav(e, window.innerWidth, 700)

    }, 

    LoggedIn(response_message) {
          if (response_message === true) {
              this.signInbtn = false
              this.dashboardbtn = true
          } else {
              this.signInbtn =  true
              this.dashboardbtn = false
              this.$router.push({name: "home"}) // protects page against unlogged users
          }
    },

   async publish() {
       if (this.pictures.length < 3) {
       alert("You need atleast 3 pictures in other to publish property")
       } else {
       const response = await API.change_status({listing_id: this.listing.id, status: "Pending"})
       if (response.message === "Success") {
       this.$router.push({ name: 'review', params: {message: 'Listing Sent'}})
       } else if (response.message === "Something went wrong, try again") {
       alert(response.message) 
       } else {
       this.$router.push({name: 'error', params: {check: true}}) //handle this error   
       }
      }
    }
  },

   async mounted () {
         
          const id = this.$route.params.id

          this.screenhandler()

          window.addEventListener("resize", this.screenhandler)

          const response = await API.loggedIn();

          this.user = response.user
          
          this.LoggedIn(response.message)

          const res = await API.get_pictures_page(id);

         if (res.message === "Bad request") {
            this.$router.push({name: 'manageListings'}) //handle this error 
         } 
         else if (res.message === "There was an error...") {
            this.$router.push({name: 'error', params: {check: true}}) //handle this error 
         }
         else {
             this.listing = res.message;
         }

         this.maximumListing(); 
        
    },
     unmounted () {
        window.removeEventListener("resize", this.screenhandler)
   },

 }
</script>



<style scoped>
