     border: 2px solid rgb(202, 201, 201);
     margin: 40px auto 70px auto;
     width: 90%;
}
 div.flex_container {
   display: flex;
   justify-content: space-around;
   width: 100%;
   margin: 20px auto 20px auto;
   font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
