}
/*DROPZONE STYLES */
#customdropzone {
    border: 2px dotted  #8a8585;
    font-family: Arial,sans-serif;
    letter-spacing: .2px;
    padding: 80px 0px;
    border-radius: 7px;
    margin-top: 40px;
    color: rgb(107, 105, 105);
    background-color: white;
    transition: .2s linear;
    font-size: 18px;
    cursor: pointer;
}
#customdropzone:hover {
    background-color: #e5e5e540;
} 
/*DROPZONE STYLES*/
div.buttons{
    display: block;
    font-size: 14px;
    width: 50%;
    font-weight: bold;
    margin: 150px auto 20px auto;
    color: white;
    text-align: center;
    padding: 10px 0px;
}
#publish_now{
    display: inline-block;
    text-decoration: none;
    position: relative;
    font-size: 14px;
    width: 100px;
    font-weight: bold;
    background-color: rgba(10, 142, 146, 0.884);
    border: 1px solid rgba(10, 142, 146, 0.884);
    cursor: pointer;
    margin: 10px 12px 20px 12px;
    color: white;
    padding: 10px 0px;
}
#publish_now span.publish_tooltip {
  visibility: hidden;
  letter-spacing: 1px;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 5px;
  border-radius: 6px;
  position: absolute;
  width: 120px;
  top: 44px;
  left: 50%;
  margin-left: -60px; /* Use half of the width (120/2 = 60), to center the tooltip */
  z-index: 1;
  font-size: 11px;
}
#publish_now:hover span.publish_tooltip {
  visibility: visible;
}
#publish_now span.publish_tooltip::after {
  content: " ";
  position: absolute;
  bottom: 100%;  /* At the top of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 3px;
  border-style: solid;
  border-color: transparent transparent black transparent;
}
#later{
    display: inline-block;
    position: relative;
    font-size: 14px;
    width: 100px;
    font-weight: bold;
    background-color: rgb(133, 8, 8);
    border: 1px solid rgb(92, 16, 16);
    cursor: pointer;
    margin: 10px 12px 20px 12px;
    color: white;
    padding: 10px 0px;
