import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

/* import the fontawesome core */
import { library } from '@fortawesome/fontawesome-svg-core'

/* import font awesome icon component */
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

/* import specific icons */
import { faUserSecret,  faNavicon, faSpinner, faHome, faLocation, faNairaSign, faBed, faShower, faToilet, faCar, faPhone, faHeart, faClock, faArrowCircleRight, faCamera, faTrash, faCirclePlus, faFilter, faEye, faPenToSquare, faImages, faUpload, faCalendarDays, faFingerprint, faClipboardCheck, faThumbsUp, faThumbsDown, faArrowRightLong, faArrowLeftLong, faXmark, faCoins, faUser, faUserFriends, faSignOut, faHandsHelping, faHouseLaptop, faCopy, faCircleCheck, faCircleXmark} from '@fortawesome/free-solid-svg-icons'

/* add icons to the library */
library.add(faUserSecret, faNavicon, faSpinner, faHome, faLocation, faNairaSign, faBed, faShower, faToilet, faCar, faPhone, faHeart, faClock, faArrowCircleRight, faCamera, faTrash, faCirclePlus, faFilter, faEye, faPenToSquare, faImages, faUpload, faCalendarDays, faFingerprint, faClipboardCheck, faThumbsUp, faThumbsDown, faArrowRightLong, faArrowLeftLong, faXmark, faCoins, faUser, faUserFriends, faSignOut, faHandsHelping, faHouseLaptop, faCopy, faCircleCheck, faCircleXmark)

/* add font awesome icon component */
Vue.component('font-awesome-icon', FontAwesomeIcon)


Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
