<div class ="avoidscams fadeindiv">
    <h1>Protect yourself against fraudsters</h1>
    <p>Please you must take note that we are not a broker. We only provide a safe platform for people who would like to rent, buy, lease or sell a house. We can safely say that most of the apartment listings on easyrentage.com are real. But you must be careful, as some people might attempt to use these platform as a means to scam tenants. So to avoid these, we came up with a few tips that can protect you against these fraudsters.</p>
     
    <h3>Avoid anyone who claims easyrentage.com or any of its partners will handle part of the transaction</h3>
    <p>As stated earlier, we are not a broker. Thats why as a landlord, you must avoid tenants who claim that easyrentage or any of its affiliates will be paying part of the renting fee. We dont and will never do that!</p>
    
    <h3>Physically identify apartment or house before making payment</h3>
    <p>Weve had cases where tenants pay for an apartment they have not seen physically and get scammed. Thats not ideal at all, and you should avoid doing that. Before making payment to a landlord on any rental listing seen on easyrentage.com, meet with the said landlord and confirm that the said apartment or house is legitimate.</p>

    <h3>Avoid properties with landlord in distant locations</h3>
    <p>Avoid renting or buying properties from landlords who claim to be far away from the said apartment.  As a tenant if you see a listing on easyrentage.com and on contacting the landlord he/she directs you to the property, but refuse to meet up with you personally. Because they claim they are not around. It is advisable to avoid such property, as it might be illegitimate.</p>

    <h3>Make research on who you are dealing with</h3>
    <p>Before making payment  it is wise to gather little information about the landlord and the property just too be sure that they actually own the property they want to lease or sell to you. One thing you can do is ask people around the neighborhood of the apartment, if the house really belongs to the said landlord. And as a landlord it is also wise to gather little information about your clients before renting out or selling your house to them.</p>
    <p>For  messages, questions or suggestions, send us an email at <a href="#">support@easyrentage.com</a> and we will get back to you as soon as possible</p>
</div> 
