<template>
    <div @click="remove_navs">
    <div class = "header">
        <ul class="stay headerlinks" v-if = "main_nav">
            <li><a class="stay" href = "#">Rent</a></li>
            <li><a class="stay" href = "#">Buy</a></li>
            <li><a class="stay" href = "#">Lease</a></li>
            <li><a class="stay" href = "#">Sell</a></li>
        </ul>
         
        <div class = "logo">
        <a href="#"><img src="@/assets/images/logo.png" id="logo" Alt = "Logo"></a>
        </div>

        <font-awesome-icon @click = "toggle_main_nav" class="remove"  icon="fa-solid fa-navicon" id="hamburger"/>
        
        <ul class="accesslinks">
        <li v-if = "signInbtn"><router-link to = "sign-in" id = "signInbtn">Sign in</router-link></li>
        <li v-if = "dashboardbtn" @click = "toggle_dashboard_nav" class="remove">Dashboard</li> 
        </ul>
</div>

<DASHBOARD :easycoins = "user.easycoins"  v-if = "dashboard_nav"/> 

<div class="container">
    <div class="Edit_details">
        <h1>Account</h1>
        <a href = "/dashboard/referrals">View Referrals</a>
        <h2>Edit details</h2>
        <form action="">
            <input type="text" placeholder="First Name">
            <input type="text" placeholder="Last Name">
            <input type="text" placeholder="Email">
            <input type="text" placeholder="Phone e.g 08135637373">
            <input type="submit" value = "Update profile">
        </form>
    </div>

    <div class="Edit_details Change_password">
        <h2>Change password</h2>
        <form action="">
            <input type="text" placeholder="Old password">
            <input type="text" placeholder="New password">
            <input type="text" placeholder="Confirm password">
            <input type="submit" value = "Update password">
        </form>
    </div> 

    <div class="Edit_details Delete_account">
        <h2>Delete Account</h2>
        <p>Once account is deleted you won't be able to restore it. Do you still want to delete?</p>
        <form action="">
            <input type="submit"  value = "Delete account permanently">
        </form>
    </div> 
    </div>
  <FOOTER/>
 </div>
