         font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
     }
     div.signupform button[type = "submit"]:hover {
        opacity: 0.9;
        border: 1px solid black;
        cursor: pointer; 
    }
    div.signupform button[type = "submit"]:disabled {
         cursor: none;
         opacity: 0.4;
    }
    p#Iagree {
    text-align: center;
    margin: 0 0px 0px 12px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 13px;
    }
    div.signupform  a{
        color: rgba(10, 142, 146, 0.884);
    }
    #have_account {
