<script>
import FOOTER from "@/components/Footer.vue"
import MIDDLEWARE from "@/middlewares/middlewares.js"
import API from "../api";
import DASHBOARD from "@/components/Dashboard.vue"

 export default {
     name: "Profile",
     components: {
         FOOTER,
         DASHBOARD,
     },
     data () {
         return {
          main_nav: false,
          dashboard_nav: false,
          screenwidth: undefined,
          signInbtn: undefined,
          dashboardbtn: undefined,

          monitor_payment: true,
          monitor_subtype1:  false,
          monitor_subtype2:  false,
          monitor_bedroom: true,

          user: undefined,

          description_length: 0,

         //FORM VALUES
          title: "",
          category: "Choose Category",
          type: "Choose Type",
          subtype: "",
          location: "",
          town: "",
          state: "Choose State",
          price: "",
          payment: "Choose Schedule",
          bedroom: "",
          bathroom: "",
          toilet: "",
          parking: "",
          description: "",
          id:  "",

          formvalues_err: {
            title_err: "",
            category_err: "",
            type_err: "",
            subtype_err: "",
            location_err: "",
            town_err: "",
            state_err: "",
            price_err: "",
            payment_err: "",
            bedroom_err: "",
            bathroom_err: undefined,
            toilets_err: undefined,
            parking_err: undefined,
            description_err: "",          
        },
          
           //VALIDATION VARIABLES
            vtitle: "",
            vcategory: "",
            vtype: "",
            vlocation: "",
            vtown: "",
            vstate: "",
            vprice: "",
            vpayment: "",
            vbedroom: "",
            vbathroom: "",
            vtoilet: "",
            vparking: "",
       }
         }, 

      watch: {
          title (newvalue) {
              this.vtitle = this.titlevalidated(newvalue);
          },
          category (newvalue) {
              this.vcategory = this.categoryvalidated(newvalue);
          },
          type (newvalue) {
              this.vtype = this.typevalidated(newvalue);
          },
          location (newvalue) {
              this.vlocation = this.locationvalidated(newvalue);
          },
          town (newvalue) {
              this.vtown = this.townvalidated(newvalue);
          },
          state (newvalue) {
              this.vstate = this.statevalidated(newvalue);
          },
          price (newvalue) {
              this.vprice = this.pricevalidated(newvalue);
          },
          payment (newvalue) {
              this.vpayment = this.paymentvalidated(newvalue);
          },
          bedroom (newvalue) {
             this.vbedroom = this.bedroomvalidated(newvalue);
          },
          bathroom (newvalue) {
              this.vbathroom = this.bathroomvalidated(newvalue);
          },
          toilet (newvalue) {
              this.vtoilet = this.toiletvalidated(newvalue);
          },
          subtype (newvalue) {
              this.subtypevalidated(newvalue);
          },
          description (newvalue) {
              this.descriptionvalidated(newvalue);
          }
      },
         

    methods: {

    screenhandler (){
          this.main_nav = MIDDLEWARE.screenhandler(window.innerWidth, 700)
     },

    toggle_main_nav () {
       this.main_nav = !this.main_nav
       this.dashboard_nav = false
         
         },

    toggle_dashboard_nav () {
       this.dashboard_nav = !this.dashboard_nav
       this.main_nav = false    
    },

     remove_navs (e) { //USED TO REMOVE ALL NAV BARS WHEN USER CLICKS ANY WHERE
      
      const class_name = e.target.className.split(" ")[0];
      
      if (class_name !== "remove") { //if the element clicked does not have the classname (remove), hide every nav on the page.
          
        this.dashboard_nav = false          

      }

     this.main_nav = MIDDLEWARE.removenav(e, window.innerWidth, 700)

    }, 

    LoggedIn(response_message) {
          if (response_message === true) {
              this.signInbtn = false
              this.dashboardbtn = true
          } else {
              this.signInbtn =  true
              this.dashboardbtn = false
              this.$router.push({name: "home"}) // protects page against unlogged users
          }
      },

        titlevalidated (value) {
        let title_length = value.length;
        if (value === "") {
        this.formvalues_err.title_err = "Please fill field";
        } else if (title_length > 70) {
        this.formvalues_err.title_err = "Title cannot be more than 70 characters";
        } else {
        this.formvalues_err.title_err = "";
        return true;
        }
    },

    categoryvalidated (value) {
       if (value === "Choose Category") {
        this.formvalues_err.category_err = "Please select category";
        //this.payment = "Choose Schedule"
        this.monitor_payment = true;
      } else if (value === "For Sell") {
        this.formvalues_err.category_err = "";
        //this.payment = ""
        this.monitor_payment = false;
        return true;
      }
       else {
        this.formvalues_err.category_err = "";
        //this.payment = "Choose Schedule"
        this.monitor_payment = true;
        return true;
      }
    },

    typevalidated (value) {
       if (value === "Choose Type") {
        this.formvalues_err.type_err = "Please select type";
        this.monitor_subtype1 = false;
        this.monitor_subtype2 = false;
        //this.subtype = ""
      } else if (value === "Flat/Apartments") {
          this.monitor_subtype1 = true;
          this.monitor_subtype2 = false;
          //this.subtype = ""
        this.formvalues_err.type_err = "";
        return true;
      } else {
         this.monitor_subtype2 = true;
         this.monitor_subtype1 = false;
         //this.subtype = ""
         this.formvalues_err.type_err = "";
         return true;
      }  
    },

    subtypevalidated (value) {
       if (value === "Single Room" || value === "Room and Palour") {
         this.monitor_bedroom = false;
         this.bedroom = 0;
       } else {
          this.monitor_bedroom = true;
      }  
    },


  locationvalidated (value) {
        let location_length = value.length;
        if (value === "") {
        this.formvalues_err.location_err = "Please fill field";
        } else if (location_length > 70) {
        this.formvalues_err.location_err = "Address cannot be more than 70 characters";
        } else {
        this.formvalues_err.location_err = "";
        return true;
        }
    },

    townvalidated (value) {
        let town_length = value.length;
        let pattern = /^[A-Za-z]+$/;
        if (value === "") {
        this.formvalues_err.town_err = "Please fill field";
        } else if (town_length > 30) {
        this.formvalues_err.town_err = "Town/village cannot be more than 30 characters";
        } else if (pattern.test(value) === false) {
        this.formvalues_err.town_err = "Town/village can only contain letters";
        } else {
        this.formvalues_err.town_err = "";
        return true;
        }
    },

      statevalidated (value) {
       if (value === "Choose State") {
        this.formvalues_err.state_err = "Please select state";
      } else {
        this.formvalues_err.state_err = "";
        return true;
      }
    },

     pricevalidated (value) {
        let price_length = value.length;
        if (value === "") {
        this.formvalues_err.price_err = "Please enter price"
        } else if (price_length > 13) {
        this.formvalues_err.price_err = "Price limit exceeded";
        this.price = this.price.slice(-13)
        alert(this.formvalues_err.price_err)
       } else {
         this.formvalues_err.price_err = "";
         return true;
        }
    },

    paymentvalidated (value) {
       if (value === "Choose Schedule") {
        this.formvalues_err.payment_err = "Please select payment schedule";
      } else {
        this.formvalues_err.payment_err = "";
        return true;
      }
    },

    bedroomvalidated (value) {
        if (value === "") { //else {
          this.formvalues_err.bedroom_err = "Please enter number of bedrooms"
        } else {
          this.formvalues_err.bedroom_err = ""
          return true;
        }
    },

    bathroomvalidated (value) {
        if (value === "") {
        this.formvalues_err.bathroom_err = "Please fill field";
        } else {
        this.formvalues_err.bathroom_err = "";
        return true;
        }
    },

    toiletvalidated (value) {
        if (value === "") {
        this.formvalues_err.toilets_err = "Please fill field";
        } else {
        this.formvalues_err.toilets_err = "";
        return true;
        }
    },

    descriptionvalidated (value) {
        this.description_length = value.length;
        if (this.description_length >= 3400) { //accept only 3000 characters for description
         this.description = value.slice(0, 3400)
         this.$refs.description_counter.attributes[2].nodeValue = "color: red;" //monitor the color of the counter
        } else {
         this.$refs.description_counter.attributes[2].nodeValue = "color: gray;"
        }
    },

    async update (page)  { //the parameter represent the page i will redirect to
        if (this.parking === "") {
            this.parking = 0;
        }
        if (this.vtitle && this.vcategory && this.vtype && this.vlocation && this.vtown && this.vstate && this.vprice && this.vpayment && this.vbedroom && this.vbathroom && this.vtoilet) {
           let formvalues = {
                title: this.title,
                category: this.category,
                type: this.type,
                subtype: this.subtype,
                location: this.location,
                town: this.town,
                state: this.state,
                price: this.price,
                payment: this.payment,
                bedroom: this.bedroom,
                bathroom: this.bathroom,
                toilet: this.toilet,
                parking: this.parking,
                description: this.description,
                id: this.id,
           }

            const response = await API.update_property_info(formvalues);

            if (response.message === "updated") { //if update is succesful, redirect user to 
                
             this.$router.push({ name: page, query: {page: 1}, params: {id: response.listing_id, message: 'Listing saved succesfully'}});

            }

       }

    },

    cancel () { //if user click cancel just redirect to manage listing page

       this.$router.push({ name: "manageListings", query: {page: 1}});

    }

}, 
  
//LIFE CYCLE HOOKS
   async mounted () {

        const id = this.$route.params.id

        this.screenhandler()
        
        window.addEventListener("resize", this.screenhandler)

        const response = await API.loggedIn();

        this.user = response.user
        
        this.LoggedIn(response.message)

        const respond = await API.edit_listing(id);

         if (respond.message === "Bad request") {
            this.$router.push({name: 'manageListings'}) //handle this error 
         } 
         else if (respond.message === "There was an error...") {
            this.$router.push({name: 'error', params: {check: true}}) //handle this error 
         }
         else {
          this.listing = respond.message;
        
          this.title  = this.listing.title
          this.category  =  this.listing.category
          this.type  = this.listing.type
          this.subtype  = this.listing.subtype
          this.location  = this.listing.location
          this.town  = this.listing.town
          this.state  = this.listing.state
          this.price  = parseFloat(this.listing.price.slice(1,).split(",").join("")); //format price properly before use
          this.payment  = this.listing.payment
          this.bedroom  = this.listing.bedroom
          this.bathroom  = this.listing.bathroom
          this.toilet  = this.listing.toilet
          this.parking  = this.listing.parking
          this.description  = this.listing.description
          this.id  =  this.listing.id
         }

},
     unmounted () {
        window.removeEventListener("resize", this.screenhandler)
   },

  created () {

      this.titlevalidated(this.title);

      this.categoryvalidated(this.category);

      this.typevalidated(this.type);

      this.locationvalidated(this.location);

      this.townvalidated(this.town);

      this.statevalidated(this.state);

      this.pricevalidated(this.price);

      this.paymentvalidated(this.payment);

      this.bedroomvalidated(this.bedroom);

      this.bathroomvalidated(this.bathroom);

      this.toiletvalidated(this.toilet);
}

 }
</script>
