<template>
<div @click = "removenav">
    <div class = "loginform">
            <h1 style="text-align: center;" >Welcome back</h1>
            <div class="validation_msg" v-if = "backend_err">
                <p id="validation_msg">{{backend_err}}</p>
            </div>
            <form>
                <input type="email" v-model = "formvalues.email" name="email" id="email" placeholder="Email">
                <p class = "err">{{formvalues_err.email_err}}</p>
                <input type="password" v-model = "formvalues.password" name="password" id="password" placeholder="Password">
                <p class = "err">{{formvalues_err.password_err}}</p>
                <button @click.prevent = "validation" type="submit" id="loginbtn" :disabled = "disablebtn"><font-awesome-icon v-if = "spinner"  class="fa-solid fa-spinner fa-spin" id = "spinner" icon="fa-solid fa-spinner"/> Login</button>
            </form>
            <a href="#" id="Fpass">Forgot password?</a>
            <p id = "dont_h_acct">Dont have an account? <a href="#" class = "signupbtn">Sign up</a></p>
        </div>
</div>
</template>



<script>
import MIDDLEWARE from "@/middlewares/middlewares.js"

import API from "../api";

export default {
  name: 'Login',
  data () {
       return {
          toggle: false,
          spinner: false,
          disablebtn: false,
          screenwidth: undefined,
          backend_err: false,

          formvalues: {
              email: "",
              password: "",
          },
          formvalues_err: {
              email_err: "",
              password_err: "",
          } 
       }
   },

   methods: {

      screenhandler (){
          this.toggle = MIDDLEWARE.screenhandler(this.screenwidth, window.innerWidth, 612)
       },

      removenav (e) {
          this.toggle = MIDDLEWARE.removenav(e, this.screenwidth, window.innerWidth, 612)
      },

      dissapear() {
          window.setTimeout( () => {
             this.backend_err = false;
          }, 5000)
      },

    emailvalidated() {
      if (this.formvalues.email === "") {
        this.formvalues_err.email_err = "Please fill field";
      } else {
        this.formvalues_err.email_err = "";
        return true;
      }
    },  

    passwordvalidated() {
      if (this.formvalues.password === "") {
        this.formvalues_err.password_err = "Please fill field";
      } else {
        this.formvalues_err.password_err = "";
        return true;
      }

    },

    async validation () {
        if (this.emailvalidated() && this.passwordvalidated()) {
           this.spinner = true;
           this.disablebtn = true;
           const response = await API.login(this.formvalues);
     
       if (response.message === "Invalid Email") {
            this.backend_err = response.message
            this.disablebtn = false;
            this.spinner = false;
            this.dissapear()
         }

        else if (response.message === "Details Correct, But Account Unverified") {      
            this.backend_err = "Verify Your Account, and Try Again"
            this.disablebtn = false;
            this.spinner = false;
            this.dissapear()
        }

        else if (response.message === "Logged in Succesfully") {      
             this.$router.push({ name: 'home', params: {message: `Welcome ${response.user.firstname}`} })
        }

        else if (response.message === "Incorrect Password") {      
            this.backend_err = response.message
            this.disablebtn = false;
            this.spinner = false;
            this.dissapear()
        }

        else {
            this.backend_err = response.message    //If there is an error from server
            this.disablebtn = false;
            this.spinner = false;
            this.dissapear()
        }
 
      }

    },

},
      //LIFE CYCLE HOOKS
    updated() {
        this.emailvalidated();
        this.passwordvalidated();
},
     mounted () {
        this.screenhandler()
        window.addEventListener("resize", this.screenhandler)
},
     unmounted () {
        window.removeEventListener("resize", this.screenhandler)
},
}
</script>

<style scoped>
