        border-radius: 3px;
    }
    p#validation_msg {
        transition: 1s;
        font-size: 12px;
        margin:0px;
    }
    div.loginform input {
        border-radius: 7px;
        border: 1px solid rgba(105, 127, 128, 0.884);
