        font-size: 14px;
        font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    }
    div.loginform button[type = "submit"] {
        border-radius: 7px;
         border: 1px solid black;
         background-color: black;
         color: white;
         padding: 6px 4px;
         display:  block;
         margin: 16px auto 17px auto;
         width: 95%;
         box-sizing: border-box;
