         font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
     }
     div.loginform input:hover{
        border: 1px solid rgba(10, 142, 146, 0.884);
        cursor: pointer; 
    }
    div.loginform input:focus{
        outline: 1px solid rgba(10, 142, 146, 0.884);
     }
     div.loginform button[type = "submit"]:hover {
        opacity: 0.9;
        border: 1px solid black;
        cursor: pointer; 
    }
    div.loginform button[type = "submit"]:disabled {
         cursor: none;
         opacity: 0.4;
    }
   div.loginform  a{
        color: rgba(10, 142, 146, 0.884);
        font-size: 13px;
    }
    div.loginform #dont_h_acct {
       font-size: 13px;
