        </form>
    <hr>
    <div class = "options">

    <div class = "option1" style="position: relative; display: inline-block;">
     <select v-model = "category">
    <option>Category</option>
    <option>For Rent</option>
    <option>For Sale</option>
     </select>
    </div>

     <div class = "option1" style="position: relative; display: inline-block;">
     <select v-model = "type">
         <option>Home Type</option>
         <option>House</option>
         <option>Flat/Apartment</option>
     </select>
    </div>

    <div class = "option1" style="position: relative; display: inline-block;">
    <select v-model = "price">
         <option>Price Range(No limit)</option>
         <option>{{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(10000))}} - {{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(50000))}}</option>
         <option>{{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(60000))}} - {{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(200000))}}</option>
         <option>{{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(210000))}} - {{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(400000))}}</option>
         <option>{{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(410000))}} - {{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(600000))}}</option>
         <option>{{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(610000))}} - {{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(800000))}}</option>
         <option>{{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(810000))}} - {{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(1000000))}}</option>
         <option>{{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(1100000))}} - {{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(10000000))}}</option>
         <option>{{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(10100000))}} - {{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(50000000))}}</option>
         <option>{{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(50100000))}} - {{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN', maximumSignificantDigits: 3 }).format(100000000))}}</option>
     </select>
    </div>

    <div class = "option1" style="position: relative; display: inline-block;">
    <select v-model = "bedrooms">
         <option>Bedrooms(No limit)</option>
