         <option>2</option>
         <option>3</option>
         <option>4</option>
         <option>5</option>
     </select>
    </div>

    <h1 id = "location">{{`${type}` === 'Home Type' ? 'Propertie': `${type}`}}s {{`${category}` === 'Category' ? 'For Rent & Sale': `${category}`}} {{`${search_data}` === '' ? '': `in ${search_data}`}} ({{all_listings.length}} found)</h1>
    <p id="result">{{`${price}` === 'Price Range(No limit)' ? '': `Price range ${price.replace(" - ", " to ")}`}}</p>
    </div>
