    <router-link style="text-decoration:none; cursor: pointer;" :to = "{ name: 'view-listing', params: { id: listing.id } }">
    <img :src = "backendUrl + listing.pictures[0]">
    <div class="house_info">
    <h1><font-awesome-icon icon="fa-solid fa-home"/> {{`${listing.bedroom}` > 0 ? `${listing.bedroom} Bedroom`: ` `}} {{listing.subtype || listing.type}} {{listing.category}}</h1>
    <h2><font-awesome-icon icon="fa-solid fa-location"/> {{`${listing.location}, ${listing.town},  ${listing.state}`.length > 70 ? `${listing.location}, ${listing.town},  ${listing.state}`.slice(0, 70) + "...": `${listing.location}, ${listing.town},  ${listing.state}`}}</h2>
    <h4>{{(new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN' }).format(listing.price))}} {{listing.payment}}</h4>
    </div>
    </router-link>
    <hr> 
    <div class = "sub_info">
    <p><font-awesome-icon icon="fa-solid fa-bed"/> bedrooms: {{listing.bedroom}}</p>|
    <p><font-awesome-icon icon="fa-solid fa-shower"/> bathrooms: {{listing.bathroom}}</p>|
    <p><font-awesome-icon icon="fa-solid fa-toilet"/> toilets: {{listing.toilet}}</p>|
    <p><font-awesome-icon icon="fa-solid fa-car"/> parking space: {{listing.parking}}</p>
    </div>
    <hr> 
    <a :href= "'tel:'+ listing.contact" id="call_owner">Call Owner</a>
    </div>
    </template>

    <h1 v-if = "paginated_listings.length === 0" id="no_property">No properties yet. Be the first to add.</h1>
    <router-link to = "/dashboard/post-listing" v-if = "paginated_listings.length === 0" id="add_property">Add property <font-awesome-icon icon="fa-solid fa-circle-plus" style="font-size:12px;"/></router-link>

   <div class = "pagination" v-if = "total_pages !== 1">
       <p ref="previous_page" style="color: black; cursor: pointer;" @click="previous_page"><font-awesome-icon icon="fa-solid fa-arrow-left-long"/></p>
       <p style = "font-size: 17px;">Page {{page_number}} of {{total_pages}}</p>
       <p ref="next_page" style="color: black; cursor: pointer;" @click="next_page"><font-awesome-icon icon="fa-solid fa-arrow-right-long"/></p>
   </div>

