<script>
import FOOTER from "@/components/Footer.vue"
import MIDDLEWARE from "@/middlewares/middlewares.js"
import API from "../api";
import DASHBOARD from "@/components/Dashboard.vue"

 export default {
     name: "for-rent",
     components: {
         FOOTER,
         DASHBOARD,
     },
     data () {
         return {
          main_nav: false,
          dashboard_nav: false,
          screenwidth: undefined,
          signInbtn: undefined,
          dashboardbtn: undefined,
          paginated_listings: undefined,
          all_listings: undefined,
          filter: undefined,
          page_number: undefined,
          total_pages: undefined,
          favorites: undefined,
          backendUrl: 'http://localhost:9000/',
          user: undefined,

          //FORM VALUES
          search_data: this.$route.query.search_data || "",
          category: this.$route.params.category || "Category",
          type:  this.$route.query.type || "Home Type",
          price: this.$route.query.price || "Price Range(No limit)",
          bedrooms: this.$route.query.bedrooms || "Bedrooms(No limit)",

       }
         },

    watch: {

     category (newvalue) {
         this.filter_category(newvalue)
     },

     type (newvalue) {
         this.filter_type(newvalue)
     },

     price (newvalue) {
         this.filter_price(newvalue)
     },

     bedrooms (newvalue) {
         this.filter_bedrooms(newvalue)
     },

    },

    methods: {

    filter_category (value) {

     window.location.assign(`http://localhost:8080/properties/${value}?page=1&search_data=${this.search_data}&type=${this.type}&price=${this.price}&bedrooms=${this.bedrooms}`)

    },

    filter_type (value) {

     window.location.assign(`http://localhost:8080/properties/${this.category}?page=1&type=${value}&search_data=${this.search_data}&price=${this.price}&bedrooms=${this.bedrooms}`)

    },

    filter_price (value) {

     window.location.assign(`http://localhost:8080/properties/${this.category}?page=1&price=${value}&type=${this.type}&search_data=${this.search_data}&bedrooms=${this.bedrooms}`)

    },

    filter_bedrooms (value) {

     window.location.assign(`http://localhost:8080/properties/${this.category}?page=1&bedrooms=${value}&type=${this.type}&search_data=${this.search_data}&price=${this.price}`)

    },

    screenhandler (){
          this.main_nav = MIDDLEWARE.screenhandler(window.innerWidth, 1027)
     },

    toggle_main_nav () {
       this.main_nav = !this.main_nav
       this.dashboard_nav = false
    },

    toggle_dashboard_nav () {
       this.dashboard_nav = !this.dashboard_nav
       this.main_nav = false
    },

    async search_property (e) {

     if (this.search_data === "") {

      e.preventDefault()

      alert("field cant be empty")
        
     }  else {

     window.location.assign(`http://localhost:8080/properties/${this.category}?page=1&search_data=${this.search_data}&type=Home Type&price=Price Range(No limit)&bedrooms=Bedrooms(No limit)`)
   
     }

    },

     remove_navs (e) { //USED TO REMOVE ALL NAV BARS WHEN USER CLICKS ANY WHERE
      
      const class_name = e.target.className.split(" ")[0];
      
      if (class_name !== "remove") { //if the element clicked does not have the classname (remove), hide every nav on the page.
          
        this.dashboard_nav = false

      }

     this.main_nav = MIDDLEWARE.removenav(e, window.innerWidth, 1027)

    }, 

    LoggedIn(response_message) {
          if (response_message === true) {
              this.signInbtn = false
              this.dashboardbtn = true
          } else {
              this.signInbtn =  true
              this.dashboardbtn = false
          }
      },

    next_page () {

     if (this.$route.query.page != this.total_pages) {

      window.location.assign(`http://localhost:8080/properties/${this.category}?page=${++this.$route.query.page}&search_data=${this.search_data}&type=${this.type}&price=${this.price}&bedrooms=${this.bedrooms}`)

      this.paginated_listings = respnse.message
         
     } else {

      this.$refs.next_page.attributes[1].nodeValue = "color: gray;"

      }

},

    previous_page () {

      if (this.$route.query.page != 1) {
            
       window.location.assign(`http://localhost:8080/properties/${this.category}?page=${--this.$route.query.page}&search_data=${this.search_data}&type=${this.type}&price=${this.price}&bedrooms=${this.bedrooms}`)

      this.paginated_listings = respnse.message

      } else {

      this.$refs.previous_page.attributes[1].nodeValue = "color: gray;"

      }

   },

   async add_to_favorites (element) {

    const listing_id = element.target.farthestViewportElement.attributes[8].nodeValue

    const response = await API.add_to_favorites({listing_id: listing_id}) 

    switch (response.message) {

        case "property saved":

        element.target.farthestViewportElement.attributes[10].nodeValue = "color: red;" //lets change the color of the heart to red
            
        break;

        case "You have this property saved already":

        element.target.farthestViewportElement.attributes[10].nodeValue = "color: white;"
            
        break;

        case "login to save property":

        alert(response.message)

        break;
    
        default:

        break;
    } 

   },

},
   async mounted () {

          const respond = await API.properties(this.$route.params.category, this.$route.query.page, this.$route.query.search_data, this.$route.query.type, this.$route.query.price, this.$route.query.bedrooms); //fetch listings according to the current category and page number

          this.paginated_listings = respond.paginatedListings //fetch all listings          

          this.all_listings = respond.all_listings

          this.favorites = respond.favorites

          this.total_pages = respond.total_pages

          this.page_number = this.$route.query.page 
       
          this.screenhandler()
          
          window.addEventListener("resize", this.screenhandler)

          const response = await API.loggedIn();

          this.user = response.user
          
          this.LoggedIn(response.message)

},
     unmounted () {
        window.removeEventListener("resize", this.screenhandler)
   },

 }
</script>
