<script>
import FOOTER from "@/components/Footer.vue"
import MIDDLEWARE from "@/middlewares/middlewares.js"
import API from "../api";
import DASHBOARD from "@/components/Dashboard.vue"

 export default {
     name: "Profile",
     components: {
         FOOTER,
         DASHBOARD,
     },
     data () {
         return {
          main_nav: false,
          dashboard_nav: false,
          screenwidth: undefined,
          signInbtn: undefined,
          dashboardbtn: undefined,
          user: undefined,
          payments_history: undefined,
          easycoin: undefined,
          input_error: '',
          input_check: '',

          quantity: 0,
          maximum_listings: 0,
          price: 0,
          transaction_charge: 0,
          total: 0,
          }

         }, 

      watch: {
          easycoin (newvalue) {
             this.input_check = this.easycoin_validation(newvalue);
          },
      },

      methods: {

    screenhandler (){
        this.main_nav = MIDDLEWARE.screenhandler(window.innerWidth, 700)
    },

    toggle_main_nav () {
       this.main_nav = !this.main_nav
       this.dashboard_nav = false
    },

    toggle_dashboard_nav () {
       this.dashboard_nav = !this.dashboard_nav
       this.main_nav = false
    },

     remove_navs (e) { //USED TO REMOVE ALL NAV BARS WHEN USER CLICKS ANY WHERE
      
      const class_name = e.target.className.toString().includes("remove");
      
      if (class_name == false) { //if the element clicked does not have the classname (remove), hide every nav on the page.
          
        this.dashboard_nav = false

      }

     this.main_nav = MIDDLEWARE.removenav(e, window.innerWidth, 700)

    }, 


    LoggedIn(response_message) {
          if (response_message === true) {
              this.signInbtn = false
              this.dashboardbtn = true
          } else {
              this.signInbtn =  true
              this.dashboardbtn = false
              this.$router.push({name: "home"}) // protects page against unlogged users
          }
    },

    easycoin_validation (value) {

    let pattern1 = /^[1-9][0-9]*$/; //accepts only numbers and do not allow leading zeros

    if (pattern1.test(value) === false) {
 
      this.input_error = "Invalid Input"

      this.quantity = 0

      this.maximum_listings = 0

      this.price =  0

      this.transaction_charge = 0
      
      this.total = 0

      return false

     } else if (value < 13) { 

      this.input_error = "The Minimum Purchase is 13 Easycoin"

      this.quantity = 0

      this.maximum_listings = 0

      this.price =  0

      this.transaction_charge = 0
      
      this.total = 0

      return false

     } else if (value.length > 6) { 

      this.input_error = "Maximum Purchase Reached"

      this.quantity = 0

      this.maximum_listings = 0

      this.price =  0

      this.transaction_charge = 0
      
      this.total = 0

      return false

     } else {

      this.quantity = Number(value)

      this.maximum_listings = this.quantity

      this.price = (this.quantity * 80)

      this.transaction_charge = (this.price * 2.5) / 100
      
      this.total = (this.price + this.transaction_charge)

      this.input_error = ""

      return true
     //do and display the necessary calculations and return true

     }

   },

 async Submit_data (e) {


     if (this.input_check) {

        let submit_btn = e

        submit_btn.target.innerHTML = 'Please wait...'

        let info = {
            email: this.user.email,
            amount: this.total * 100,
            quantity: this.quantity,
            maximum_listings: this.maximum_listings,
        }

       const response = await API.submit_payment(info)

       if (response === "There was an error") {

        alert("Something went wrong")

        submit_btn.target.innerHTML = 'Pay <svg data-v-07fb7554="" aria-hidden="true" focusable="false" data-prefix="fas" data-icon="arrow-right-long" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" class="svg-inline--fa fa-arrow-right-long" style="float: right;"><path data-v-07fb7554="" fill="currentColor" d="M502.6 278.6c12.5-12.5 12.5-32.8 0-45.3l-128-128c-12.5-12.5-32.8-12.5-45.3 0s-12.5 32.8 0 45.3L402.7 224 32 224c-17.7 0-32 14.3-32 32s14.3 32 32 32l370.7 0-73.4 73.4c-12.5 12.5-12.5 32.8 0 45.3s32.8 12.5 45.3 0l128-128z" class=""></path></svg>'

       } else {

       window.location.replace(response.data.authorization_url)

      }
         
    }

   }

}, 
  
//LIFE CYCLE HOOKS
   async mounted () {

          this.screenhandler()
          
          window.addEventListener("resize", this.screenhandler)

          const response = await API.loggedIn();

          this.user = response.user

          this.payments_history = response.user.payments_history
          
          this.LoggedIn(response.message)

},
     unmounted () {
        window.removeEventListener("resize", this.screenhandler)
   },
 }
</script>
