<template>
<div @click = "removenav">
    <div class = "header">
        <ul class="stay headerlinks" v-if = "main_nav">
            <li><a class="stay" href = "#">Home</a></li>
        </ul>
         
        <div class = "logo">
        <a href="#"><img src="@/assets/images/logo.png" id="logo" Alt = "Logo"></a>
        </div>

        <font-awesome-icon @click = "toggle_main_nav"  icon="fa-solid fa-navicon" id="hamburger"/>
        
</div>

<DASHBOARD v-if = "side_nav" class = "remain"/>

 <div class="container">
 <div class = "flex_container">
    <div class="pictures">
        <h1>Pictures ({{pictures.length}})</h1>
    <template v-for = "filename in pictures">
    <div class="photos" ref="photos">
        <img :src = "backendUrl + filename">
    </div>
    </template>
    
    </div>

    <div class="property_info">
     <h1>Property Info</h1>
     <hr>

    <h2>{{listing.title}}</h2>

     <b>Property id</b>
     <p>{{listing.id}}</p>

     <b>Views</b>
     <p>{{listing.views}}</p>

     <b>Category</b>
     <p>{{listing.category}}</p>

     <b>Type</b>
     <p>{{listing.type}}</p>

      <b>Sub type</b>
     <p>{{listing.subtype}}</p>

     <b>Location</b>
     <p>{{listing.location}}, {{listing.town}}, {{listing.state}}.</p>

     <b>Dated Added</b>
     <p>{{listing.dateAdded}}</p>

     <b>Price</b>
     <p>{{listing.price}} {{listing.payment}}</p>

      <b>Description</b>
     <p>{{listing.description}}</p>

      <b>bedroom</b>
     <p>{{listing.bedroom}}</p>

      <b>Bathrooom</b>
     <p>{{listing.bathroom}}</p>

      <b>Toilet</b>
     <p>{{listing.toilet}}</p>

      <b>Parking</b>
     <p>{{listing.parking}}</p>

    </div>


    
    <div class="property_info">
     <h1>Landlord Info</h1>
     <hr>

    <h2>{{user.email}}</h2>

     <b>First name</b>
     <p>{{user.firstname}}</p>

     <b>Last name</b>
     <p>{{user.lastname}}</p>

     <b>Status</b>
     <p>{{user.status}}</p>

     <b>Number of listings</b>
     <p>{{user.listings.length}}</p>

      <b>Account creation date</b>
     <p>{{user.acctCreation}}</p>

    </div>

</div>
<div class="buttons">

<p @click = "Approve(listing.id)" id="approve">Approve <font-awesome-icon icon="fa-solid fa-thumbs-up"/>
</p>

<p @click = "Decline(listing.id)" id="decline">Decline <font-awesome-icon icon="fa-solid fa-thumbs-down"/>
</p>
 
</div>

</div>
 <FOOTER/>
</div>
</template>

<script>
import FOOTER from "@/components/Footer.vue"
import MIDDLEWARE from "@/middlewares/middlewares.js"
import API from "@/api";
import DASHBOARD from "@/components/Dashboard.vue"

 export default {
     name: "postPictures",
     components: {
         FOOTER,
         DASHBOARD,
     },
     data () {
         return {
          main_nav:  false,
          side_nav: false,
          screenwidth: undefined,
          listing: undefined,
          pictures: undefined,
          user: undefined,
          backendUrl: 'http://localhost:9000/',
    }
},
  methods: {

    screenhandler () {
          this.main_nav = MIDDLEWARE.screenhandler(this.screenwidth, window.innerWidth, 700)
   },

    removenav (e) {
          this.main_nav = MIDDLEWARE.removenav(e, this.screenwidth, window.innerWidth, 700)
          this.side_nav = MIDDLEWARE.removesidenav(e)
    },

    toggle_side_nav() {
       this.side_nav = !this.side_nav
       this.main_nav = MIDDLEWARE.screenhandler(this.screenwidth, window.innerWidth, 700)
    },

    toggle_main_nav() {
       this.main_nav = !this.main_nav
       this.side_nav = false
    },

  async Approve (listing_id) {

       const response = await API.get_singleListing(listing_id)

       this.user = response.user

       const approve = await API.approve_decline({listing_id: listing_id, user_email: this.user.email, firstname: this.user.firstname, status: "approved"})

       if (approve.message === "Listing was approved") {
           
        alert(approve.message)

        location.assign('http://localhost:8080/private/admin');
 
       } else {

        alert(approve.message)

       }

    },

    async Decline (listing_id) {

       const response = await API.get_singleListing(listing_id)

       this.user = response.user

       const decline = await API.approve_decline({listing_id: listing_id, user_email: this.user.email, firstname: this.user.firstname, status: "declined"})

       if (decline.message === "Listing was declined") {
           
        alert(decline.message)

        location.assign('http://localhost:8080/private/admin');
 
       } else {

        alert(decline.message)

       }

    }

  },

   async mounted () {
         
          const id = this.$route.params.id

          this.screenhandler()

          window.addEventListener("resize", this.screenhandler)

          const response = await API.get_singleListing(id)

          if (response.message === "something went wrong") {
              
           alert(response.message)

          } else {

           this.listing = response.listing

           this.user = response.user

           this.pictures = this.listing.pictures

           console.log(this.user)

          }
        
    },
     unmounted () {
        window.removeEventListener("resize", this.screenhandler)
   },

 }
</script>



