<template>
    <div @click="remove_navs">

    <div class = "header">
        <ul class="stay headerlinks" v-if = "main_nav">
            <li><a class="stay" href = "#">Rent</a></li>
            <li><a class="stay" href = "#">Buy</a></li>
            <li><a class="stay" href = "#">Lease</a></li>
            <li><a class="stay" href = "#">Sell</a></li>
        </ul>
         
        <div class = "logo">
        <a href="#"><img src="@/assets/images/logo.png" id="logo" Alt = "Logo"></a>
        </div>

        <font-awesome-icon @click = "toggle_main_nav" class="remove"  icon="fa-solid fa-navicon" id="hamburger"/>
        
</div>

    <div class = "signupform">
            <h1 style="text-align: center;">Create Account</h1>
            <div class="validation_msg" v-if = "backend_err">
                <p id="validation_msg">{{backend_err}}</p>
            </div>
            <form>
                <input name="firstname" v-model = "formvalues.firstname" id="firstname" placeholder="First Name">
                <p class = "err">{{formvalues_err.firstname_err}}</p>
                <input name="lastname" v-model = "formvalues.lastname" id="lastname" placeholder="Last Name">
                <p class = "err">{{formvalues_err.lastname_err}}</p>
                <input type="text" v-model = "formvalues.phone" name="phone" id="phone" placeholder="phone">
                <p class = "err">{{formvalues_err.phone_err}}</p>
                <input type="email" v-model = "formvalues.email" name="email" id="email" placeholder="Email">
                <p class = "err">{{formvalues_err.email_err}}</p>
                <input type="password" v-model = "formvalues.password" name="password" id="password" placeholder="Password">
                <p class = "err">{{formvalues_err.password_err}}</p>
                <input type="text" v-model = "formvalues.referral_code" name="referral_code" id="referral_code" placeholder="referral_code" readonly>
                <p class = "err" style="text-align: center; color: gray;">referral code</p>
            
                <button @click.prevent = "validation" type="submit" id="signupbtn" :disabled = "disablebtn"><font-awesome-icon v-if = "spinner"  class="fa-solid fa-spinner fa-spin" id = "spinner" icon="fa-solid fa-spinner"/> Sign up</button>

                <p id = "Iagree"  >By submitting, i accept easy rentage <a href = "#"  >terms of use</a></p>
            </form>  
            <p id = "have_account" >Already have an account? <a href="#" class = "loginbtn" >Log in</a></p>
        </div> 

    </div>
</template>

<script>
import MIDDLEWARE from "@/middlewares/middlewares.js"
import API from "../api";

export default {
  name: 'Register',
  data () {
       return {
          toggle: false,
          spinner: false,
          disablebtn: false,
          screenwidth: undefined,
          backend_err: false,
          user: undefined,
          main_nav: false,
          dashboard_nav: false,
          screenwidth: undefined,
          signInbtn: undefined,
          dashboardbtn: undefined,

          formvalues: {
              firstname: "",
              lastname: "",
              phone: "",
              email: "",
              password: "",
              referral_code: "",
          },
          formvalues_err: {
              firstname_err: "",
              lastname_err: "",
              phone_err: "",
              email_err: "",
              password_err: "",
              referral_code_err: "",
          } 
       }
   },

//METHODS
   methods: {

    screenhandler (){
          this.main_nav = MIDDLEWARE.screenhandler(window.innerWidth, 1027)
     },

    toggle_main_nav () {
       this.main_nav = !this.main_nav
       this.dashboard_nav = false
    },

    toggle_dashboard_nav () {
       this.dashboard_nav = !this.dashboard_nav
       this.main_nav = false
    },

      remove_navs (e) { //USED TO REMOVE ALL NAV BARS WHEN USER CLICKS ANY WHERE
      
      const class_name = e.target.className.split(" ")[0];
      
      if (class_name !== "remove") { //if the element clicked does not have the classname (remove), hide every nav on the page.
          
        this.dashboard_nav = false

      }

     this.main_nav = MIDDLEWARE.removenav(e, window.innerWidth, 1027)

    }, 

      dissapear() {
          window.setTimeout( () => {
             this.backend_err = false;
          }, 5000)
      },

      LoggedIn(response_message) {
          if (response_message === true) {
              this.$router.push({name: "home"}) // protects page against unlogged users
          } else {
              this.$router.push({name: "referral-signup", params: {id: this.$route.params.id}}) // protects page against unlogged users
          }
      },
    
      firstnamevalidated() {
      let pattern = /^[A-Za-z]+$/;
      if (this.formvalues.firstname === "") {
        this.formvalues_err.firstname_err = "Please fill field";
      } else if (pattern.test(this.formvalues.firstname) === false) {
       this.formvalues_err.firstname_err = "Firstname can only contain letters";
      } else {
        this.formvalues_err.firstname_err = "";
        return true;
      }
    },

      lastnamevalidated() {
      let pattern = /^[A-Za-z]+$/;
      if (this.formvalues.lastname === "") {
        this.formvalues_err.lastname_err = "Please fill field";
      } else if (pattern.test(this.formvalues.lastname) === false) {
       this.formvalues_err.lastname_err = "Lastname can only contain letters";
      } else {
        this.formvalues_err.lastname_err = "";
        return true;
      }
    },

    phonevalidated() {
     let pattern = /^[0-9]*$/;
     let phone_length = this.formvalues.phone.length;
      if (this.formvalues.phone === "") {
        this.formvalues_err.phone_err = "Please fill field";
      } else if (pattern.test(this.formvalues.phone) === false) {
        this.formvalues_err.phone_err = "Invalid phone number";
      } else if (phone_length > 11) {
        this.formvalues_err.phone_err = "Cannot be 12 digits long";
      } else {
        this.formvalues_err.phone_err = "";
        return true;
      }
    },

     emailvalidated() {
     let pattern = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
      if (this.formvalues.email === "") {
        this.formvalues_err.email_err = "Please fill field";
      } else if (pattern.test(this.formvalues.email) === false) {
        this.formvalues_err.email_err = "Invalid email";
      } else {
        this.formvalues_err.email_err = "";
        return true;
      }
    },

    passwordvalidated() {
      let pass_length = this.formvalues.password.length;
      if (this.formvalues.password === "") {
        this.formvalues_err.password_err = "Please fill field";
      } else if (pass_length < 7 || pass_length > 15) {
        this.formvalues_err.password_err = "Must be between 7 and 15 characters long";
      } else {
        this.formvalues_err.password_err = "";
        return true;
      }
    },

    async validation () {
        if (this.firstnamevalidated() && this.lastnamevalidated() && this.phonevalidated() && this.emailvalidated() && this.passwordvalidated()) {
           this.spinner = true;
           this.disablebtn = true;
           const response = await API.register(this.formvalues);
       
       if (response.message === "Email Already Exists") {
            this.backend_err = response.message
            this.disablebtn = false;
            this.spinner = false;
            this.dissapear()
        }

        else if (response.message === "Error Creating Account, Try again..") {
            this.backend_err = response.message
            this.disablebtn = false;
            this.spinner = false;
            this.dissapear()
        }

        else {
             this.$router.push({ name: 'email-activation', params: {check: true, confirmationCode: response.confirmationCode} })
        }
       }
    }

},
//LIFE CYCLE HOOKS
    updated() {
        this.firstnamevalidated();
        this.lastnamevalidated();
        this.phonevalidated();
        this.emailvalidated();
        this.passwordvalidated();
     },
    async mounted () {

        this.formvalues.referral_code = this.$route.params.id

        this.screenhandler()

        window.addEventListener("resize", this.screenhandler)

        const response = await API.loggedIn();

        this.user = response.user
          
        this.LoggedIn(response.message)
},
     unmounted () {
        window.removeEventListener("resize", this.screenhandler)
},
}
</script>


