<template>
    <div class = "container" v-if = "check">
   <h1>Check your email inbox</h1>
   <p>We sent an email link to complete your registration.</p>
   <p><b>Tip:</b> Check your spam folder in case the email was incorrectly identified.</p>

   <h3>Didn't receive the email?</h3>
   <button @click.prevent = "sendMail" id="btn" :disabled = "disablebtn"><font-awesome-icon v-if = "spinner"  class="fa-solid fa-spinner fa-spin" id = "spinner" icon="fa-solid fa-spinner"/>Resend Email</button>
    </div>
</template>


<script>
import API from "../api";

export default {

   name: "Email-Activation",

   data () {

    return {

     check: false,
     spinner: false,
     disablebtn: false,

    }

   },

   methods: {

  async sendMail () {
      this.spinner = true;
      this.disablebtn = true;
      const response = await API.ResendConfirmationMail({confirmationCode: this.$route.params.confirmationCode})

      if (response.message === "Account created succesfully, Mail sent") {
            alert("Email was Resent")
            this.disablebtn = false;
            this.spinner = false;
        }

      else if (response.message === "Email not sent") {
            alert("Email not sent. Try again..")
            this.disablebtn = false;
            this.spinner = false;
        }

     else {

      this.$router.push({name: 'error', params: {check: true}}) //If there is an error from the catch block in server

    }  

  },

    },

// LIFE CYCLE HOOKS

   // LIFE CYCLE HOOKS
  mounted () {

    this.check = this.$route.params.check //Stops unauthorized viewers

  }
    
}
</script>

<style scoped>
/* DESKTOPS, LAPTOPS DEVICES */
@media only screen and (min-width: 612px) {
   body {
        margin: 0;
        padding: 0;
    }
    div.container {
        width: 500px;
        margin: 130px auto 0px auto;
        text-align: center;
        border: 3px solid rgba(10, 142, 146, 0.884);
        padding: 20px;
    }
    div.container h1{
     font-size: 19px;   
     margin: 0 0 0 0; 
    }
    div.container p{
     font-size: 16px;   
     margin: 10 0 0 0; 
    }
    div.container h3{
     font-size: 13px;   
     margin: 30px 0 2px 0; 
    }
    #btn {
      cursor: pointer;
      padding: 2px 5px;
      margin-top: 10px;
    }
    #btn:disabled {
         cursor: none;
         opacity: 0.9;
    }

}

/* TABLETS, MOBILE DEVICES */
@media only screen and (max-width:612px) {
    body {
        margin: 0;
        padding: 0;
    }
    div.container {
        width: 70%;
        margin: 130px auto 0px auto;
        text-align: center;
        border: 3px solid rgba(10, 142, 146, 0.884);
        padding: 20px;
    }
    div.container h1{
     font-size: 18px;   
     margin: 0 0 0 0; 
    }
    div.container p{
     font-size: 15px;   
     margin: 10 0 0 0; 
    }
    div.container h3{
     font-size: 12px;   
     margin: 30px 0 2px 0; 
    }
    #btn {
      cursor: pointer;
      padding: 2px 5px;
      margin-top: 10px;
    }
    #btn:disabled {
         cursor: none;
         opacity: 0.9;
    }
}
</style>