<div class ="fadeindiv">
    <h1>Privacy and Policy</h1>
    <p>This privacy policy is designed to inform users of Easy Rentage website and any related application, about how Easy Rentage gathers and uses Personal Information submitted to Easy Rentage through the Website. <b>Note that the word "Landlord" refers to anybody selling a house or renting out/leasing an apartment. While "Tenant" refers to anybody renting an apartment or buying a home.</b> Easy Rentage will take reasonable steps to protect user privacy consistent with the guidelines set forth in this Privacy Policy. In this Privacy Policy, "user" or "you" means any person viewing the Website or submitting any Personal Information to Easy Rentage in connection with using the Website. Be rest assured that your informations are safe with us. And by using the Website, you are indicating your consent to this Privacy Policy. IF YOU DO NOT AGREE WITH THIS PRIVACY POLICY, YOU SHOULD NOT USE THE WEBSITE.</p>
     
    <h3>Why we collect your Information</h3>
    <p>We collect your informations so as to make your experience with us pleasurable. If you are a Landlord for example who wants to sell a house or lease an apartment, we require certain details of the house to enable tenants find your house.</p>
    <p>As for your personal informations such as <b>Name, Email e.t.c </b>which are collected during sign up. Are collected, so as to keep track of your visit to our website and update you on the changes on these site. Be rest assured that your personal informations are not shared or sold to any third party site.</p>
    
    <h3>Amendments</h3>
    <p>Take note that this policy might be amended in the future, but be sure that what ever changes are made will be communicated to you through the email on your profile.</p>
    <p>For  messages, questions or suggestions, send us an email at <a href="#">support@easyrentage.com</a> and we will get back to you as soon as possible</p>
</div> 
