         <p>Your purchase of {{info.quantity}} Easycoin worth {{new Intl.NumberFormat('en-NG', { style: 'currency', currency: 'NGN' }).format(info.amount)}} was succesfull.</p>
         <p>click on dashboard to view your balance.</p>

     </div>

     <div class = "modal" v-if = "payment_status === 'payment failed'">    

        <font-awesome-icon  icon="fa-solid fa-circle-xmark" id = "declined_emoji"/>
         
