<script>
import FOOTER from "@/components/Footer.vue"
import MIDDLEWARE from "@/middlewares/middlewares.js"
import API from "../api";
import DASHBOARD from "@/components/Dashboard.vue"

 export default {
     name: "Profile",
     components: {
         FOOTER,
         DASHBOARD,
     },
     data () {
         return {
          main_nav: false,
          dashboard_nav: false,
          screenwidth: undefined,
          signInbtn: undefined,
          dashboardbtn: undefined,
          user: undefined,
          easycoin: undefined,
          payment_status: undefined,
          info: undefined
          }

         }, 

      methods: {

    screenhandler (){
        this.main_nav = MIDDLEWARE.screenhandler(window.innerWidth, 700)
    },

    toggle_main_nav () {
       this.main_nav = !this.main_nav
       this.dashboard_nav = false
    },

    toggle_dashboard_nav () {
       this.dashboard_nav = !this.dashboard_nav
       this.main_nav = false
    },

     remove_navs (e) { //USED TO REMOVE ALL NAV BARS WHEN USER CLICKS ANY WHERE
      
      const class_name = e.target.className.toString().includes("remove");
      
      if (class_name == false) { //if the element clicked does not have the classname (remove), hide every nav on the page.
          
        this.dashboard_nav = false

      }

     this.main_nav = MIDDLEWARE.removenav(e, window.innerWidth, 700)

    }, 


    LoggedIn(response_message) {
          if (response_message === true) {
              this.signInbtn = false
              this.dashboardbtn = true
          } else {
              this.signInbtn =  true
              this.dashboardbtn = false
              this.$router.push({name: "home"}) // protects page against unlogged users
          }
    },

}, 
  
//LIFE CYCLE HOOKS
   async mounted () {

          const respond = await API.verify_payment({reference: this.$route.query.reference, email: this.$route.query.email, quantity: Number(this.$route.query.quantity), amount: Number(this.$route.query.amount) / 100, maximum_listings: this.$route.query.maximum_listings})

          this.payment_status = respond.message

          if (this.payment_status === "reference exists already") {
              
           window.location.replace("http://localhost:8080/")

          }

          this.info = respond.info

          this.user = respond.user

          this.screenhandler()
          
          window.addEventListener("resize", this.screenhandler)

          const response = await API.loggedIn();
          
          this.LoggedIn(response.message)

},

     unmounted () {
        window.removeEventListener("resize", this.screenhandler)
   },
 }
</script>
