<script>
import FOOTER from "@/components/Footer.vue"
import MIDDLEWARE from "@/middlewares/middlewares.js"
import API from "../api";
import DASHBOARD from "@/components/Dashboard.vue"

export default {
  name: 'Home',
  components: {
      FOOTER,
      DASHBOARD,
  },
  data () {
       return {
          main_nav: false,
          dashboard_nav: false,
          screenwidth: undefined,
          signInbtn: undefined,
          dashboardbtn: undefined,
          search_data: "",
          longitude: "",
          latitude: "",
          listings: undefined,
          favorites: undefined,
          user: undefined,
          backendUrl: 'http://localhost:9000/',
       }
   },
   methods: {

   screenhandler (){
          this.main_nav = MIDDLEWARE.screenhandler(window.innerWidth, 742)
     },

    toggle_main_nav () {
       this.main_nav = !this.main_nav
       this.dashboard_nav = false
    },

    toggle_dashboard_nav () {
       this.dashboard_nav = !this.dashboard_nav
       this.main_nav = false
    },

    async search_property (e) {

     if (this.search_data === "") {

      e.preventDefault()

      alert("field cant be empty")
        
     }  else {

     window.location.assign(`http://localhost:8080/properties/Category?page=1&search_data=${this.search_data}&type=Home Type&price=Price Range(No limit)&bedrooms=Bedrooms(No limit)`)
   
     }

   },

     async remove_navs (e) { //USED TO REMOVE ALL NAV BARS WHEN USER CLICKS ANY WHERE
      
      const class_name = e.target.className.toString()
      
      if (class_name.includes("remove") == false) { //if the element clicked does not have the classname (remove), hide every nav on the page.
          
        this.dashboard_nav = false

        this.user.logincounter = 1 //update user logincounter in template

        await API.update_login_counter() //update user logincounter in database

   }

     this.main_nav = MIDDLEWARE.removenav(e, window.innerWidth, 742)

    }, 


      LoggedIn(response_message) {
          if (response_message === true) {
              this.signInbtn = false
              this.dashboardbtn = true
          } else {
              this.signInbtn =  true
              this.dashboardbtn = false
          }
      },

      get_coordinates() {

        if (navigator.geolocation) {

        navigator.geolocation.getCurrentPosition( (position) => {    
     
        this.longitude = position.coords.longitude,

        this.latitude = position.coords.latitude
        
      });

    } else { 

     console.log("Geolocation is not supported by this browser.")
  
    }

  },


   async add_to_favorites (element) {

    const listing_id = element.target.farthestViewportElement.attributes[8].nodeValue

    const response = await API.add_to_favorites({listing_id: listing_id}) 

    switch (response.message) {

        case "property saved":

        element.target.farthestViewportElement.attributes[10].nodeValue = "color: red;" //lets change the color of the heart to red
            
        break;

        case "You have this property saved already":

        element.target.farthestViewportElement.attributes[10].nodeValue = "color: white;"
            
        break;

        case "login to save property":

        alert(response.message)

        window.location.assign("http://localhost:8080/sign-in")

        break;
    
        default:

        break;
    } 

   },

},
     created () {

        this.get_coordinates()
   
     },

     async mounted () {

          const response = await API.loggedIn();

          this.user = response.user
          
          this.LoggedIn(response.message)

          const respond = await API.fetch_home_recommendations({longitude: 11.206567, latitude: 10.38301});

          this.listings = respond.listings

          this.favorites = respond.favorites
       
          this.screenhandler()
          
          window.addEventListener("resize", this.screenhandler)
          
  },

     unmounted () {

        window.removeEventListener("resize", this.screenhandler)
   
   }
}
</script>

