import axios from "axios"
import { data } from "vue2-dropzone"
 

export default class API {

    //POST REQUESTS

    //Register
    static async register(formdata) {
        const res = await axios.post("/register", formdata)
        return res.data
    }

    //Resend Confirmation Email
    static async ResendConfirmationMail(info) {
        const res = await axios.post('/resend-email', info)
        return res.data
    }

    //Login
    static async login(formdata) {
        const res = await axios.post("/login", formdata)
        return res.data
    }

    //Submit Listing
    static async submit_listing(formdata) {
        const res = await axios.post("/submit_listing", formdata)
        return res.data
    }

    //Submit pictures
    static async submit_pictures(listing_id) {
        const res = await axios.post("/submit_pictures", listing_id)
        return res.data
    }

    //Add/Remove property from favorites
    static async add_to_favorites(listing_id) {
        const res = await axios.post("/add_to_favorites", listing_id)
        return res.data
    }

    //filter property
    static async filter_property(state, type, price, bedrooms) {
        const res = await axios.post("/filter_property")
          return res.data
    }

     //fetch home recommendations
     static async fetch_home_recommendations(geo_info) {
        const res = await axios.post("/fetch_home_recommendations", geo_info)
        return res.data
     }

    //submit payment
    static async submit_payment(info) {
    const res = await axios.post("/submit_payment", info)
    return res.data
    }

    //verify payment
    static async verify_payment(reference) {
        const res = await axios.post("/verify_payment", reference)
        return res.data
        }


    //GET REQUESTS

    //Verify Email
    static async emailVerification(id) {
        const res = await axios.get(`/email-confirmation/${id}`)
        return res.data
    }

    //Get home page
    static async loggedIn() {
        const res = await axios.get("/loggedIn")
        return res.data
    }

      //Verify Email
      static async emailVerification(id) {
        const res = await axios.get(`/email-confirmation/${id}`)
        return res.data
    }

    //get post listing page
    static async postListing() {
        const res = await axios.get("/dashboard/post-listing")
        return res.data
    }

    //get manage listing page
    static async get_managelisting_page(page_number, cate_gory) {
        const res = await axios.get(`/dashboard/manage-listings`, {
            params: {
                page: page_number,
                category: cate_gory,
            }
        })
        return res.data
    }

    //get saved properties page
    static async get_savedproperties_page(page_number) {
        const res = await axios.get(`/dashboard/saved-properties`, {
            params: {
                page: page_number,
            }
        })
        return res.data
    }

    //fetch all properties
    static async properties(category, page_number, search_data, type, price, bedrooms) {
        const res = await axios.get(`/properties/${category}`, {
            params: {
                page: page_number,
                search_data: search_data,
                type: type,
                price: price,
                bedrooms: bedrooms,
            }
        })
        return res.data
    }

    //get pictures page by id
    static async get_pictures_page(id) {
        const res = await axios.get(`/dashboard/post-pictures/${id}`)
        return res.data
    }

    //view listing
    static async view_listing(id) {
        const res = await axios.get(`/view-listing/${id}`)
        return res.data
    }

     //edit listing
     static async edit_listing(id) {
        const res = await axios.get(`/dashboard/edit-listing/${id}`)
        return res.data
    }

    //get user
    static async fetch_user() {
       const res = await axios.get("/fetch_user");
       return res.data
    }

    //fetch user's referrals
    static async fetch_referrals() {
        const res = await axios.get("/dashboard/referrals");
        return res.data
     }

    
    //PATCH REQUESTS

    //change listing status
    static async change_status(listing_id) {
        const res = await axios.patch(`/change_status`, listing_id)
        return res.data
    }

    //update property information
    static async update_property_info(formvalue) {
        const res = await axios.patch(`/update_property_info`, formvalue)
        return res.data
    }

     //update logincounter
     static async update_login_counter() {
        const res = await axios.patch(`/update_login_counter`)
        return res.data
    }

    //show filter in managelisting page
    static async filter(parameter) {
        const res = await axios.patch("/filter", parameter)
        return res.data
    }
    
    //DELETE REQUESTS

    //delete pictures
    static async delete_pictures(imgsrc) {
        const res = await axios.delete(`/delete_pictures`, imgsrc)
        return res.data
    }

    //delete individual listing
    static async delete_listing(listing_id) {
        const res = await axios.delete(`/delete_listing`, listing_id)
        return res.data
    }

    //delete listings categorically
    static async delete_listings_categorically(category) {
        const res = await axios.delete(`/delete_listings_categorically`, category)
        return res.data
    }

    //remove all properties from favorites
    static async remove_all_properties() {
        const res = await axios.delete(`/remove_all_properties`)
        return res.data
    }
    
    //ADMIN ROUTE
  
   //get pending listing
   static async get_pendingListings () {
       const res = await axios.get("/private/admin");
       return res.data
    }
  
  //get single pending listing
  static async get_singleListing (id) {
      const res = await axios.get(`/private/admin/listing/${id}`);
      return res.data
    }

  //approve or decline listing
  static async approve_decline (data) {
    const res = await axios.post('/approve_decline', data);
    return res.data
  }

}