<template>
    <div @click="remove_navs">

    <div class = "header">
        <ul class="stay headerlinks" v-if = "main_nav">
            <li><a class="stay" href = '/properties/For Rent?page=1&search_data=&type=Home Type&price=Price Range(No limit)&bedrooms=Bedrooms(No limit)'>Rent</a></li>
            <li><a class="stay" href = '/properties/For Sale?page=1&search_data=&type=Home Type&price=Price Range(No limit)&bedrooms=Bedrooms(No limit)'>Buy</a></li>
            <li><a class="stay" href = "#">Lease</a></li>
            <li><a class="stay" href = "#">Sell</a></li>
        </ul>
         
        <div class = "logo">
        <a href="#"><img src="@/assets/images/logo.png" id="logo" Alt = "Logo"></a>
        </div>

        <font-awesome-icon @click = "toggle_main_nav" class="remove"  icon="fa-solid fa-navicon" id="hamburger"/>
        
        <ul class="accesslinks">
        <li v-if = "signInbtn"><router-link to = "sign-in" id = "signInbtn">Sign in</router-link></li>
        <li v-if = "dashboardbtn" @click = "toggle_dashboard_nav" class="remove">Dashboard</li> 
        </ul>
    </div>

    <DASHBOARD :easycoins = "user.easycoins" v-if = "dashboard_nav"/>

    <div class = "container">
    <h1>Invite Friends</h1>

    <div class = "sub_container">
    <img src="@/assets/images/gift_box.jpg">
    <h2>Invite Friend, Get 100 Easycoin</h2>
    <p>Get 100 Easycoin when someone signs up using your referral link. All you have to do is share your referral link with your friends. When they sign up and verify their account using your link, you will be rewarded with 100 Easycoin.</p>
    <label>Your Invite Link</label>
    <div class="tooltip">
    <input type = "text" ref = "myInput" :value = "referral_link + user.new_referral_code" disabled>
    <button @click.prevent="myFunction()" @mouseout="outFunc()">
    <span class="tooltiptext" ref ="myTooltip">Copy to clipboard</span>
    Copy <font-awesome-icon icon="fa-solid fa-copy"/>
    </button>
    </div>
    <a href = "/dashboard/referrals">View Referrals</a>
    </div>
    </div>  
   <FOOTER />
    </div>
</template>

<script>
import FOOTER from "@/components/Footer.vue"
import MIDDLEWARE from "@/middlewares/middlewares.js"
import API from "../api";
import DASHBOARD from "@/components/Dashboard.vue"

export default {
  name: 'Home',
  components: {
      FOOTER,
      DASHBOARD,
  },
  data () {
       return {
          main_nav: false,
          dashboard_nav: false,
          screenwidth: undefined,
          signInbtn: undefined,
          dashboardbtn: undefined,
          user: undefined,
          backendUrl: 'http://localhost:9000/',
          referral_link: `http://localhost:8080/referral-signup/`
       }
   },
   methods: {

   screenhandler (){
          this.main_nav = MIDDLEWARE.screenhandler(window.innerWidth, 742)
     },

    toggle_main_nav () {
       this.main_nav = !this.main_nav
       this.dashboard_nav = false
    },

    toggle_dashboard_nav () {
       this.dashboard_nav = !this.dashboard_nav
       this.main_nav = false
    },

     async remove_navs (e) { //USED TO REMOVE ALL NAV BARS WHEN USER CLICKS ANY WHERE
      
      const class_name = e.target.className.toString()
      
      if (class_name.includes("remove") == false) { //if the element clicked does not have the classname (remove), hide every nav on the page.
          
        this.dashboard_nav = false

   }

     this.main_nav = MIDDLEWARE.removenav(e, window.innerWidth, 742)

    }, 


      LoggedIn(response_message) {
          if (response_message === true) {
              this.signInbtn = false
              this.dashboardbtn = true
          } else {
              this.signInbtn =  true
              this.dashboardbtn = false
          }
      }, 

     myFunction() {
 
        let copyText = this.$refs.myInput;

        copyText.select();

        copyText.setSelectionRange(0, 99999);

        navigator.clipboard.writeText(copyText.value);

        let tooltip =  this.$refs.myTooltip;

        tooltip.innerHTML = "Copied";
    },

   outFunc() {

        let tooltip = this.$refs.myTooltip;

        tooltip.innerHTML = "Copy to clipboard";
   }
  
  },

     async mounted () {

          const response = await API.loggedIn();

          this.user = response.user
          
          this.LoggedIn(response.message)           
       
          this.screenhandler()
          
          window.addEventListener("resize", this.screenhandler)
          
  },

     unmounted () {

        window.removeEventListener("resize", this.screenhandler)
   
   }
}
</script>

<style scoped>
