}
div.sub_container button {
     border: 1px solid gray;
     padding: 5px 5px;
     color: green;
     width: calc(100% - 80%);
     cursor: pointer;
     font-size: 14px;
}
.tooltip .tooltiptext {
  visibility: hidden;
  width: 140px;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  bottom: 150%;
  left: 50%;
  opacity: 0;
  transition: opacity 0.3s;
}
.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}
button:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}
div.sub_container a{
       color: rgba(10, 142, 146, 0.884);
       display: block;
       margin: 15px 0px;
       text-align: center;
}

}
