<template>
   <div class = "container">
   <p>{{message}}</p>
   <router-link to = "/Sign-in" id = "loginbtn">Log in</router-link>
    </div>
</template>

<script>
import API from "../api";

export default {

    name: "Email-Confirmation",

    data () {
        
        return {
           message: ""
        }
    },
    
   async mounted () {
       
      const response = await API.emailVerification(this.$route.params.id)

      if (response.message === "Email Verification Succesful") {
           
       this.message = response.message

      } 
      else if (response.message === "Email is Already Verified") {
          
       this.message = response.message

      } 
      else if (response.message === "Email Verification Failed") {
        
        this.$router.push({name: 'home'})
        
      }
      else {
          
        this.$router.push({name: 'error', params: {check: true}}) //If there is an error from the catch block in server

      }
       
    }
}
</script>


