<template>
    <div @click = "removenav">
    <div class = "header">
        <ul class="stay headerlinks" v-if = "main_nav">
            <li><a class="stay" href = "#">Home</a></li>
        </ul>
         
        <div class = "logo">
        <a href="#"><img src="@/assets/images/logo.png" id="logo" Alt = "Logo"></a>
        </div>

        <font-awesome-icon @click = "toggle_main_nav"  icon="fa-solid fa-navicon" id="hamburger"/>
      
</div>
 
<DASHBOARD v-if = "side_nav" class = "remain"/> <br>

<div class = "container">
    <h1>Pending Properties ({{listings_length}})</h1>
    <h2 v-if="listings_length === 0" style="font-size: 19px; margin: 80px 0px 50px; 0px; text-align: center;">No Pending Listings</h2>
    <div class = "listings">   

    <template v-for = "listing in listings"> 
    <div class="houses">
    <router-link :to = "{ name: 'adminView', params: { id: listing.id } }" class="link">
    <img src="@/assets/images/house.jpg">
    <div class="house_info">
    <h1><font-awesome-icon icon="fa-solid fa-home"/> {{listing.title}}</h1>
    <h2><font-awesome-icon icon="fa-solid fa-location"/> {{listing.location}} {{listing.town}}, {{listing.state}}.</h2>
    <p><font-awesome-icon icon="fa-solid fa-fingerprint" style="font-size: 11px;"/> Property Id: <b>{{listing.id}}</b></p>
    <p><font-awesome-icon icon="fa-solid fa-calendar-days" style="font-size: 11px;"/> Date Added: <b>{{listing.dateAdded}}</b></p>
    <p><font-awesome-icon icon="fa-solid fa-clipboard-check" style="font-size: 11px;"/> Status: <b>{{listing.status}}</b></p>
    <p> Price: <b>{{listing.price}} {{listing.payment}}</b></p>
    </div>
    </router-link>

    <hr> 
    <div class = "sub_info">
    <p @click  = "Approve(listing.id)" class = "sub_info_child" to = "#"><font-awesome-icon icon="fa-solid fa-thumbs-up" /> Approve</p>
    <router-link :to = "{ name: 'adminView', params: { id: listing.id } }" class = "sub_info_child"><font-awesome-icon icon="fa-solid fa-eye"/> View</router-link>
    <p @click = "Decline(listing.id)" class = "sub_info_child" to = "#"><font-awesome-icon icon="fa-solid fa-thumbs-down" /> Reject</p>
    </div>
    </div>
    </template>
    </div>

</div>
 <FOOTER/>
      </div>
</template>

<script>
import FOOTER from "@/components/Footer.vue"
import MIDDLEWARE from "@/middlewares/middlewares.js"
import API from "@/api";
import DASHBOARD from "@/components/Dashboard.vue"

 export default {
     name: "manageListings",
     components: {
         FOOTER,
         DASHBOARD,
     },
     data () {
         return {
          main_nav: false,
          side_nav: false,
          screenwidth: undefined,
          signInbtn: undefined,
          listings: undefined,
          user: undefined,
          listings_length: undefined,
         }
     },

      methods: {

      screenhandler (){
          this.main_nav = MIDDLEWARE.screenhandler(this.screenwidth, window.innerWidth, 600)
     },

      removenav (e) {
          this.main_nav = MIDDLEWARE.removenav(e, this.screenwidth, window.innerWidth, 600)
          this.side_nav = MIDDLEWARE.removesidenav(e)
     },
    
    toggle_main_nav() {
       this.main_nav = !this.main_nav
       this.side_nav = false
    },

    async Approve (listing_id) {

       const response = await API.get_singleListing(listing_id)

       this.user = response.user

       const approve = await API.approve_decline({listing_id: listing_id, user_email: this.user.email, firstname: this.user.firstname, status: "approved"})

       if (approve.message === "Listing was approved") {
           
        alert(approve.message)

        location.assign('http://localhost:8080/private/admin');
 
       } else {

        alert(approve.message)

       }

    },

    async Decline (listing_id) {

       const response = await API.get_singleListing(listing_id)

       this.user = response.user

       const decline = await API.approve_decline({listing_id: listing_id, user_email: this.user.email, firstname: this.user.firstname, status: "declined"})

       if (decline.message === "Listing was declined") {
           
        alert(decline.message)

        location.assign('http://localhost:8080/private/admin');
 
       } else {

        alert(decline.message)

       }

    }

  },

     async mounted () {

        this.screenhandler()

        window.addEventListener("resize", this.screenhandler)

        const response = await API.get_pendingListings()

        this.listings = response.message

        this.listings_length = this.listings.length
  },
     unmounted () {
        window.removeEventListener("resize", this.screenhandler)
   }
 }
</script>

