module.exports = class MIDDLEWARES {


   //Monitors Screen Width For Main Nav
    static screenhandler (windowscreen, mediaquerywidth) { //if user resizes screen, it checks user screen if he don pass 700(desktop screen). if he don pass, keep on displaying the main nav
         if (windowscreen >= mediaquerywidth) {   
            return true
            } else { //hide am
             return false
         }
      }

   //USED TO REMOVE MAIN NAV WHEN USER CLICKS ANY WHERE AND SCREEN SIZE LESS THAN 700
      static removenav (e, windowscreen, mediaquerywidth) { //if the element clicked on does not have classname of stay and screensize less than 700 hide the main nav
         const element = e.target.className.split(" ")
         if (element[0] !== "stay" && windowscreen <= mediaquerywidth) {
            return false
         } else { //do not hide the main nav
            return true
         }
      }
       
}