        </div>

        <font-awesome-icon @click = "toggle_main_nav"  icon="fa-solid fa-navicon" id="hamburger"/>
        
        <ul class="accesslinks">
        <li v-if = "signInbtn"><router-link to = "sign-in" id = "signInbtn">Sign in</router-link></li>
        <li v-if = "dashboardbtn" @click = "toggle_side_nav" class = "remain">Dashboard</li> 
        </ul>
</div>

<DASHBOARD :easycoins = "user.easycoins"  v-if = "side_nav" class = "remain"/>

<div class ="fadeindiv contactusdetails">
