    <p>You can reach us through the following details provided below. Our support team will get back to you as soon as possible. call the phone no or send a mail to the email address. You can also use the form provided below, your message will be received.</h2>
    <h2>Email: support@easyrentage.com</h2>
    <h2>Phone No: +234 7063131606</h2>
    
