<template>
<div @click="remove_navs">
    <div class = "header">
        <ul class="stay headerlinks" v-if = "main_nav">
            <li><a class="stay" href = "#">Rent</a></li>
            <li><a class="stay" href = "#">Buy</a></li>
            <li><a class="stay" href = "#">Lease</a></li>
            <li><a class="stay" href = "#">Sell</a></li>
        </ul>
         
        <div class = "logo">
        <a href="#"><img src="@/assets/images/logo.png" id="logo" Alt = "Logo"></a>
        </div>

        <font-awesome-icon @click = "toggle_main_nav" class="remove"  icon="fa-solid fa-navicon" id="hamburger"/>
        
        <ul class="accesslinks">
        <li v-if = "signInbtn"><router-link to = "sign-in" id = "signInbtn">Sign in</router-link></li>
       <li v-if = "dashboardbtn" @click = "toggle_dashboard_nav" class="remove">Dashboard</li> 
        </ul>
</div>

<DASHBOARD :easycoins = "user.easycoins" v-if = "dashboard_nav"/>

 <div class="container">

 <div class="lightbox" v-if="!main_nav"> <!-- show for mobile devices -->
    <img :src = "pictures[0]" @click = "index = 0"> 
    <font-awesome-icon @click = "index = 0" icon="fa-solid fa-arrow-right-long" id="arrow_right"/>
    <b id="pix_indicator" @click = "index = 0">1 of 3</b>
  <Viewer
    :index = "index"
    :items = "pictures"
    :toolbar = "toolbar"
    showGallery = true
    :clickOutsideHide = "hide"
    enableScrollLock = false
    @close = "close"
  />
</div>

<div class="lightbox" v-if="main_nav"> <!-- show for desktops/laptops devices -->
    <template v-for = "picture in pictures">
    <div class="grid-item"><img :src = "picture" @click = "index = 0"></div>
    </template>
  <Viewer
    :index = "index"
    :items = "pictures"
    :toolbar = "toolbar"
    showGallery = true
    galleryPosition = "bottom"
    :clickOutsideHide = "hide"
    enableScrollLock = false
    @close = "close"
  />
</div>

<div class = "informations">
<div class = "first_info">
<p class="title_price"><font-awesome-icon icon="fa-solid fa-home"/> 5 bed room flat</p>
<p class="title_price">{{listing.price}} {{listing.payment}}</p>
<p style="font-weight: lighter;">{{listing.location}} {{listing.town}}, {{listing.state}} state.</p>
<p style="font-size: 12px;">For rent</p>
<a :href= "'tel:'+ user.phone">Call Owner</a>
</div>

<hr>

<div class = "second_info">
<p><font-awesome-icon icon="fa-solid fa-bed" style="font-size: 12px;"/> 2 Bedroom</p>
<p><font-awesome-icon icon="fa-solid fa-shower" style="font-size: 12px;"/> 2 Bathrooom</p>
<p><font-awesome-icon icon="fa-solid fa-toilet" style="font-size: 12px;"/> 2 Toilet</p>
<p><font-awesome-icon icon="fa-solid fa-car" style="font-size: 12px;"/> 3 Parking Spaces</p>
</div>

<hr>

<div class = "third_info">
<h2 style = "font-size: 14px; margin: 0;">Property Description</h2>
