<h2 style = "font-size: 14px; margin: 0;">Owner's Info</h2>

<form action="">

   <div class="grid_container">

    <div class = "item1">
    <label for = "firstname">First Name</label> <input type="text" id="firstname" name = "firstname" v-model = "user.firstname" disabled>
    </div>
  
    <div class = "item2">
    <label for = "lastname">Last Name</label> <input type="text" id="lastname" name = "lastname" v-model = "user.lastname" disabled>
    </div>
  
