    <label for = "email">Email</label> <input type="text" id="email" name = "email" v-model = "user.email" disabled>
    </div>

    <div class = "item4">
    <label for = "phone">Phone</label> <input type="text" id="phone" name = "phone" v-model = "user.phone" disabled>
    </div>

    </div>
</form>

