        </div>

        <font-awesome-icon @click = "toggle_main_nav"  icon="fa-solid fa-navicon" id="hamburger"/>
        
        <ul class="accesslinks">
        <li v-if = "signInbtn"><router-link to = "sign-in" id = "signInbtn">Sign in</router-link></li>
        <li v-if = "dashboardbtn" @click = "toggle_side_nav" class = "remain">Dashboard</li> 
        </ul>
</div>

<DASHBOARD :easycoins = "user.easycoins" v-if = "side_nav" class = "remain"/>

<div class ="aboutus fadeindiv">
    <h1>About Easy Rentage</h1>
    <p>Easy Rentage is a platform that aims to solve the long lasting problem of housing in nigeria. We studied and discovered that the issue of getting an apartment to rent or a house to buy is alot of stress for the average Nigerian. Thats why we provided these platform where anyone can easily get an apartment or home of their choice. Not only these, this platform also provides an avenue for those who want to rent out/lease an apartment or sell a home. Our mission is to put a smile on your faces, by providing these platform for your sole use which we are optimistic will solve housing problem.</p>
</div> 
