<script>
import FOOTER from "@/components/Footer.vue"
import MIDDLEWARE from "@/middlewares/middlewares.js"
import API from "../api";
import DASHBOARD from "@/components/Dashboard.vue"

export default {
  name: 'about',
  components: {
      FOOTER,
      DASHBOARD,
  },
  data () {
       return {
          main_nav: false,
          side_nav: false,
          screenwidth: undefined,
          signInbtn: undefined,
          dashboardbtn: undefined,
          user: undefined,
       }
   },
   methods: {
      screenhandler (){
          this.main_nav = MIDDLEWARE.screenhandler(this.screenwidth, window.innerWidth, 742)
       },

      removenav (e) {
          this.main_nav = MIDDLEWARE.removenav(e, this.screenwidth, window.innerWidth, 742)
          this.side_nav = MIDDLEWARE.removesidenav(e)
      },

      toggle_side_nav() {
       this.side_nav = !this.side_nav
       this.main_nav = MIDDLEWARE.screenhandler(this.screenwidth, window.innerWidth, 742)
     },

      toggle_main_nav() {
       this.main_nav = !this.main_nav
       this.side_nav = false
    },

     LoggedIn(response_message) {
          if (response_message === true) {
              this.signInbtn = false
              this.dashboardbtn = true
          } else {
              this.signInbtn =  true
              this.dashboardbtn = false
          }
      }
},
   async mounted () {
       
          this.screenhandler()
          
          window.addEventListener("resize", this.screenhandler)

          const response = await API.loggedIn();

          this.user = response.user
          
          this.LoggedIn(response.message)
},
     unmounted () {
        window.removeEventListener("resize", this.screenhandler)
   }
}
</script>

